var poaaText1=[{title:"\xA7 55-508. Applicability.",text:["A. This chapter shall apply to developments subject to a declaration, as defined herein, initially recorded after January 1, 1959, associations incorporated or otherwise organized after such date, and all subdivisions created under the former Subdivided Land Sales Act (\xA7 55-336 et seq.). For the purposes of this chapter, as used in the former Subdivided Land Sales Act, the terms:","\"Covenants,\" \"deed restrictions,\" or \"other recorded instruments\" for the management, regulation and control of a development shall be deemed to correspond with the term \"declaration\";","\"Developer\" shall be deemed to correspond with the term \"declarant\";","\"Lot\" shall be deemed to correspond with the term \"lot\"; and","\"Subdivision\" shall be deemed to correspond with the term \"development.\"","B. This chapter shall be deemed to supersede the former Subdivided Land Sales Act (\xA7 55-336 et seq.), and no development shall be established under the latter on or after July 1, 1998.","This chapter shall not be construed to affect the validity of any provision of any declaration recorded prior to July 1, 1998, provided, however, that this chapter shall be applicable to any development established prior to the enactment of the former Subdivided Land Sales Act (\xA7 55-336 et seq.)(i) located in a county with an urban county executive form of government, (ii) containing 500 or more lots, (iii) each lot of which is located within the boundaries of a watershed improvement district established pursuant to Article 3 (\xA7 10.1-614 et seq.) of Chapter 6 of Title 10.1, and (iv) each lot of which is subject to substantially similar deed restrictions, which shall be considered a declaration under this chapter.","In addition, any development established prior to the enactment of the former Subdivided Land Sales Act (\xA7 55-336 et seq.) may specifically provide for the applicability of the provisions of this chapter.","C. This chapter shall not be construed to affect the validity of any provision of any prior declaration; however, to the extent the declaration is silent, the provisions of this chapter shall apply. If any one lot in a development is subject to the provisions of this chapter, all lots in the development shall be subject to the provisions of this chapter notwithstanding the fact that such lots would otherwise be excluded from the provisions of this chapter. Notwithstanding any provisions of this chapter, a declaration may specifically provide for the applicability of the provisions of this chapter. The granting of rights in this chapter shall not be construed to imply that such rights did not exist with respect to any development created in the Commonwealth before July 1, 1989.","D. This chapter shall not apply to the (i) provisions of documents of, (ii) operations of any association governing, or (iii) relationship of a member to any association governing condominiums created pursuant to the Condominium Act (\xA7 55-79.39 et seq.), cooperatives created pursuant to the Virginia Real Estate Cooperative Act (\xA7 55-424 et seq.), time-shares created pursuant to the Virginia Real Estate Time-Share Act (\xA7 55-360 et seq.), or membership campgrounds created pursuant to the Virginia Membership Camping Act (\xA7 59.1-311 et seq.). This chapter shall not apply to any nonstock, nonprofit, taxable corporation with nonmandatory membership which, as its primary function, makes available golf, ski and other recreational facilities both to its members and the general public."]},{title:"\xA7 55-509. Definitions.",text:["As used in this chapter, unless the context requires a different meaning:","\"Act\" means the Virginia Property Owners' Association Act.","\"Association\" means the property owners' association.","\"Board of directors\" means the executive body of a property owners' association, or a committee which is exercising the power of the executive body by resolution or bylaw.","\"Capital components\" means those items, whether or not a part of the common area, for which the association has the obligation for repair, replacement or restoration and for which the board of directors determines funding is necessary.","\"Common area\" means property within a development which is owned, leased or required by the declaration to be maintained or operated by a property owners' association for the use of its members and designated as common area in the declaration.","\"Common interest community\" means the same as that term is defined in \xA7 55-528.","\"Common interest community manager\" means the same as that term is defined in \xA7 54.1-2345.","\"Declarant\" means the person or entity signing the declaration and its successors or assigns who may submit property to a declaration.","\"Declaration\" means any instrument, however denominated, recorded among the land records of the county or city in which the development or any part thereof is located, that either (i) imposes on the association maintenance or operational responsibilities for the common area or (ii) creates the authority in the association to impose on lots, or on the owners or occupants of such lots, or on any other entity any mandatory payment of money in connection with the provision of maintenance and/or services for the benefit of some or all of the lots, the owners or occupants of the lots, or the common area. \"Declaration\" includes any amendment or supplement to the instruments described in this definition. \"Declaration\" shall not include a declaration of a condominium, real estate cooperative, time-share project or campground.","\"Development\" means real property located within this Commonwealth subject to a declaration which contains both lots, at least some of which are residential or are occupied for recreational purposes, and common areas with respect to which any person, by virtue of ownership of a lot, is a member of an association and is obligated to pay assessments provided for in a declaration.","\"Disclosure packet update\" means an update of the financial information referenced in subdivisions A 2 through A 9 of \xA7 55-509.5. The update shall include a copy of the original disclosure packet.","\"Financial update\" means an update of the financial information referenced in subdivisions A 2 through A 7 of \xA7 55-509.5.","\"Lot\" means (i) any plot or parcel of land designated for separate ownership or occupancy shown on a recorded subdivision plat for a development or the boundaries of which are described in the declaration or in a recorded instrument referred to or expressly contemplated by the declaration, other than a common area, and (ii) a unit in a condominium association or a unit in a real estate cooperative if the condominium or cooperative is a part of a development.","\"Lot owner\" means one or more persons who own a lot, including any purchaser of a lot at a foreclosure sale, regardless of whether the deed is recorded in the land records where the lot is located. \"Lot owner\" does not include any person holding an interest in a lot solely as security for a debt.","\"Meeting\" or \"meetings\" means the formal gathering of the board of directors where the business of the association is discussed or transacted.","\"Professionally managed\" means a common interest community that has engaged (i) a common interest community manager to provide management services to the community or (ii) a person as an employee for compensation to provide management services to the community, other than a resident of the community who provides bookkeeping, billing, or recordkeeping services for that community.","\"Property owners' association\" or \"association\" means an incorporated or unincorporated entity upon which responsibilities are imposed and to which authority is granted in the declaration.","\"Settlement agent\" means the same as that term is defined in \xA7 55-525.16."]},{title:"\xA7 55-509.1. Developer to register and file annual report; payment of real estate taxes attributable to the common area upon transfer to association.",text:["A. (Effective July 1, 2019) Unless control of the association has been transferred to the members, the developer shall register the association with the Common Interest Community Board within 30 days after recordation of the declaration and thereafter shall ensure that the report required pursuant to \xA7 55-516.1 has been filed.","B. Upon the transfer of the common area to the association, the developer shall pay all real estate taxes attributable to the open or common space as defined in \xA7 58.1-3284.1 through the date of the transfer to the association."]},{title:"\xA7 55-509.1:1. Limitation on certain contracts and leases by declarant.",text:["A. If entered into any time prior to the expiration of the period of declarant control contemplated by the declaration, no contract or lease entered into with the declarant or any entity controlled by the declarant, management contract, or employment contract that is directly or indirectly made by or on behalf of the association, its board of directors, or the lot owners as a group shall be entered into for a period in excess of five years. Any such contract or agreement may be terminated without penalty by the association or its board of directors upon not less than 90 days' written notice to the other party given no later than 60 days after the expiration of the period of declarant control contemplated by the declaration.","B. If entered into any time prior to the expiration of the period of declarant control contemplated by the declaration, any contract or lease entered into with the declarant or any entity controlled by the declarant, management contract, or employment contract that is directly or indirectly made by or on behalf of the association, its board of directors, or the lot owners as a group may be renewed for periods not in excess of five years; however, at the end of any five-year period, the association or its board of directors may terminate any further renewals or extensions thereof.","C. If entered into at any time prior to the expiration of the period of declarant control contemplated by the declaration, any contract, lease, or agreement, other than those subject to the provisions of subsection A or B, may be entered into by or on behalf of the association, its board of directors, or the lot owners as a group if such contract, lease, or agreement is bona fide and is commercially reasonable to the association at the time entered into under the circumstances.","D. This section shall be strictly construed to protect the rights of the lot owners."]},{title:"\xA7 55-509.2. Documents to be provided by declarant upon transfer of control.",text:["Unless previously provided to the board of directors of the association, once the majority of the members of the board of directors other than the declarant are owners of improved lots in the association and the declarant no longer holds a majority of the votes in the association, the declarant shall provide to the board of directors or its designated agent the following: (i) all association books and records held by or controlled by the declarant, including without limitation, minute books and rules and regulations and all amendments thereto which may have been promulgated; (ii) a statement of receipts and expenditures from the date of the recording of the association documents to the end of the regular accounting period immediately succeeding the first election of the board of directors by the home owners, not to exceed 60 days after the date of the election, such statement being prepared in an accurate and complete manner, utilizing the accrual method of accounting; (iii) the number of lots subject to the declaration; (iv) the number of lots that may be subject to the declaration upon completion of development; (v) a copy of the latest available approved plans and specifications for all improvements in the project or as-built plans if available; (vi) all association insurance policies which are currently in force; (vii) written unexpired warranties of the contractors, subcontractors, suppliers, and manufacturers, if any, relative to all common area improvements; (viii) any contracts in which the association is a contracting party; (ix) a list of manufacturers of paints, roofing materials and other similar materials if specified for use on the association property; and (x) the number of members of the board of directors and number of such directors appointed by the declarant together with names and contact information of members of the board of directors.","If the association is managed by a common interest community manager in which the declarant, or its principals, has no pecuniary interest or management role, then such common interest community manager shall have the responsibility to provide the documents and information required by clauses (i), (ii), (vi), and (viii)."]},{title:"\xA7 55-509.3. Association charges.",text:["Except as expressly authorized in this chapter, in the declaration, or otherwise provided by law, no association may (i) make an assessment or impose a charge against a lot or a lot owner unless the charge is a fee for services provided or related to use of the common area or (ii) charge a fee related to the provisions set out in \xA7 55-509.6 or 55-509.7 that is not expressly authorized in those sections. Nothing in this chapter shall be construed to authorize an association or common interest community manager to charge an inspection fee for an unimproved or improved lot except as provided in \xA7 55-509.6 or 55-509.7. The Common Interest Community Board may assess a monetary penalty for a violation of this section against any (a) association pursuant to \xA7 54.1-2351 or (b) common interest community manager pursuant to \xA7 54.1-2349, and may issue a cease and desist order against the violator pursuant to \xA7 54.1-2349 or 54.1-2352, as applicable."]},{title:"\xA7 55-509.3:1. Rental of lots.",text:["A. Except as expressly authorized in this chapter or in the declaration or as otherwise provided by law, no association shall:","1. Condition or prohibit the rental to a tenant of a lot by a lot owner or make an assessment or impose a charge except as provided in \xA7 55-509.3;","2. Charge a rental fee, application fee, or other processing fee of any kind in excess of $50 during the term of any lease;","3. Charge an annual or monthly rental fee or any other fee not expressly authorized in \xA7 55-509.3;","4. Require the lot owner to use a lease or an addendum to the lease prepared by the association;","5. Charge any deposit from the lot owner or the tenant of the lot owner; or","6. Have the authority to evict a tenant of any lot owner or to require any lot owner to execute a power of attorney authorizing the association to so evict. However, if the lot owner designates a person licensed under the provisions of \xA7 54.1-2106.1 as the lot owner's authorized representative with respect to any lease, the association shall recognize such representation without a formal power of attorney, provided that the association is given a written authorization signed by the lot owner designating such representative. Notwithstanding the foregoing, the requirements of \xA7 55-515 and the declaration shall be satisfied before any such representative may exercise a vote on behalf of a lot owner as a proxy.","B. The association may require the lot owner to provide the association with the names and contact information of the tenants and authorized occupants under such lease and any authorized agent of the lot owner, and vehicle information for such tenants or authorized occupants. The association may require the lot owner to provide the association with the tenant's acknowledgement of and consent to any rules and regulations of the association.","C. The provisions of this section shall not apply to lots owned by the association."]},{title:"\xA7 55-509.3:2. Statement of lot owner rights.",text:["Every lot owner who is a member in good standing of a property owners' association shall have the following rights:","1. The right of access to all books and records kept by or on behalf of the association according to and subject to the provisions of \xA7 55-510, including records of all financial transactions;","2. The right to cast a vote on any matter requiring a vote by the association's membership in proportion to the lot owner's ownership interest, except to the extent that the declaration provides otherwise;","3. The right to have notice of any meeting of the board of directors, to make a record of such meetings by audio or visual means, and to participate in such meeting in accordance with the provisions of subsection G of \xA7 55-510 and \xA7 55-510.1;","4. The right to have (i) notice of any proceeding conducted by the board of directors or other tribunal specified in the declaration against the lot owner to enforce any rule or regulation of the association and (ii) the opportunity to be heard and represented by counsel at the proceeding, as provided in \xA7 55-513, and the right of due process in the conduct of that hearing; and","5. The right to serve on the board of directors if duly elected and a member in good standing of the association, except to the extent the declaration provides otherwise.","The rights enumerated in this section shall be enforceable by any such lot owner pursuant to the provisions of \xA7 55-515."]},{title:"\xA7 55-509.4. Contract disclosure statement; right of cancellation; use of for sale sign in connection with resale; designation of authorized representative.",text:["A. Subject to the provisions of subsection A of \xA7 55-509.10, an owner selling a lot shall disclose in the contract that (i) the lot is located within a development that is subject to the Virginia Property Owners' Association Act (\xA7 55-508 et seq.); (ii) the Act requires the seller to obtain from the property owners' association an association disclosure packet and provide it to the purchaser; (iii) the purchaser may cancel the contract within three days after receiving the association disclosure packet or being notified that the association disclosure packet will not be available; (iv) if the purchaser has received the association disclosure packet, the purchaser has a right to request an update of such disclosure packet in accordance with subsection H of \xA7 55-509.6 or subsection D of \xA7 55-509.7, as appropriate; and (v) the right to receive the association disclosure packet and the right to cancel the contract are waived conclusively if not exercised before settlement.","For purposes of clause (iii), the association disclosure packet shall be deemed not to be available if (a) a current annual report has not been filed by the association with either the State Corporation Commission pursuant to \xA7 13.1-936 or with the Common Interest Community Board pursuant to \xA7 55-516.1, (b) the seller has made a written request to the association that the packet be provided and no such packet has been received within 14 days in accordance with subsection A of \xA7 55-509.5, or (c) written notice has been provided by the association that a packet is not available.","B. If the contract does not contain the disclosure required by subsection A, the purchaser's sole remedy is to cancel the contract prior to settlement.","C. The information contained in the association disclosure packet shall be current as of a date specified on the association disclosure packet prepared in accordance with this section; however, a disclosure packet update or financial update may be requested in accordance with subsection G of \xA7 55-509.6 or subsection D of \xA7 55-509.7, as appropriate. The purchaser may cancel the contract: (i) within three days after the date of the contract, if on or before the date that the purchaser signs the contract, the purchaser receives the association disclosure packet or is notified that the association disclosure packet will not be available; (ii) within three days after receiving the association disclosure packet if the association disclosure packet or notice that the association disclosure packet will not be available is hand delivered, delivered by electronic means, or delivered by a commercial overnight delivery service or the United States Postal Service, and a receipt obtained; or (iii) within six days after the postmark date if the association disclosure packet or notice that the association disclosure packet will not be available is sent to the purchaser by United States mail. The purchaser may also cancel the contract at any time prior to settlement if the purchaser has not been notified that the association disclosure packet will not be available and the association disclosure packet is not delivered to the purchaser.","Notice of cancellation shall be provided to the lot owner or his agent by one of the following methods:","1. Hand delivery;","2. United States mail, postage prepaid, provided the sender retains sufficient proof of mailing, which may be either a United States postal certificate of mailing or a certificate of service prepared by the sender confirming such mailing;","3. Electronic means provided the sender retains sufficient proof of the electronic delivery, which may be an electronic receipt of delivery, a confirmation that the notice was sent by facsimile, or a certificate of service prepared by the sender confirming the electronic delivery; or","4. Overnight delivery using a commercial service or the United States Postal Service.","In the event of a dispute, the sender shall have the burden to demonstrate delivery of the notice of cancellation. Such cancellation shall be without penalty, and the seller shall cause any deposit to be returned promptly to the purchaser.","D. Whenever any contract is canceled based on a failure to comply with subsection A or C or pursuant to subsection B, any deposit or escrowed funds shall be returned within 30 days of the cancellation, unless the parties to the contract specify in writing a shorter period.","E. Any rights of the purchaser to cancel the contract provided by this chapter are waived conclusively if not exercised prior to settlement.","F. Except as expressly provided in this chapter, the provisions of this section and \xA7 55-509.5 may not be varied by agreement, and the rights conferred by this section and \xA7 55-509.5 may not be waived.","G. For purposes of this chapter:","\"Delivery\" means that the disclosure packet is delivered to the purchaser or purchaser's authorized agent by one of the methods specified in this section.","\"Purchaser's authorized agent\" means any person designated by such purchaser in a ratified real estate contract for purchase and sale of residential real property or other writing designating such agent.","\"Receives, received, or receiving\" the disclosure packet means that the purchaser or purchaser's authorized agent has received the disclosure packet by one of the methods specified in this section.","\"Seller's authorized agent\" means a person designated by such seller in a ratified real estate contract for purchase and sale of residential real property or other writing designating such agent.","H. Unless otherwise provided in the ratified real estate contract or other writing, delivery to the purchaser's authorized agent shall require delivery to such agent and not to a person other than such agent. Delivery of the disclosure packet may be made by the lot owner or the lot owner's authorized agent.","I. If the lot is governed by more than one association, the purchaser's right of cancellation may be exercised within the required time frames following delivery of the last disclosure packet or resale certificate.","J. Except as expressly authorized in this chapter or in the declaration or as otherwise provided by law, no property owners' association shall:","1. Require the use of any for sale sign that is (i) an association sign or (ii) a real estate sign that does not comply with the requirements of the Real Estate Board. An association may, however, prohibit the placement of signs in the common area and establish reasonable rules and regulations that regulate (a) the number of real estate signs to be located on real property upon which the owner has a separate ownership interest or a right of exclusive possession so long as at least one real estate sign is permitted; (b) the geographical location of real estate signs on real property in which the owner has a separate ownership interest or a right of exclusive possession, so long as the location of the real estate signs complies with the requirements of the Real Estate Board; (c) the manner in which real estate signs are affixed to real property; and (d) the period of time after settlement when the real estate signs on such real property shall be removed; or","2. Require any lot owner to execute a formal power of attorney if the lot owner designates a person licensed under the provisions of \xA7 54.1-2106.1 as the lot owner's authorized representative, and the association shall recognize such representation without a formal power of attorney, provided that the association is given a written authorization signed by the lot owner designating such representative. Notwithstanding the foregoing, the requirements of \xA7 13.1-849 of the Virginia Nonstock Corporation Act (\xA7 13.1-801 et seq.) and the association's declaration, bylaws, and articles of incorporation shall be satisfied before any such representative may exercise a vote on behalf of a lot owner as a proxy."]},{title:"\xA7 55-509.5. Contents of association disclosure packet; delivery of packet.",text:["A. The association shall deliver, within 14 days after receipt of a written request and instructions by a seller or the seller's authorized agent, an association disclosure packet as directed in the written request. The information contained in the association disclosure packet shall be current as of a date specified on the association disclosure packet. If hand or electronically delivered, the written request is deemed received on the date of delivery. If sent by United States mail, the request is deemed received six days after the postmark date. An association disclosure packet shall contain the following:","1. The name of the association and, if incorporated, the state in which the association is incorporated and the name and address of its registered agent in Virginia;","2. A statement of any expenditure of funds approved by the association or the board of directors that shall require an assessment in addition to the regular assessment during the current year or the immediately succeeding fiscal year;","3. A statement, including the amount of all assessments and any other mandatory fees or charges currently imposed by the association, together with any post-closing fee charged by the common interest community manager, if any, and associated with the purchase, disposition, and maintenance of the lot and to the right of use of common areas, and the status of the account;","4. A statement of whether there is any other entity or facility to which the lot owner may be liable for fees or other charges;","5. The current reserve study report or summary thereof, a statement of the status and amount of any reserve or replacement fund, and any portion of the fund allocated by the board of directors for a specified project;","6. A copy of the association's current budget or a summary thereof prepared by the association, and a copy of its statement of income and expenses or statement of its financial position (balance sheet) for the last fiscal year for which such statement is available, including a statement of the balance due of any outstanding loans of the association;","7. A statement of the nature and status of any pending suit or unpaid judgment to which the association is a party and that either could or would have a material impact on the association or its members or that relates to the lot being purchased;","8. A statement setting forth what insurance coverage is provided for all lot owners by the association, including the fidelity bond maintained by the association, and what additional insurance would normally be secured by each individual lot owner;","9. A statement that any improvement or alteration made to the lot, or uses made of the lot or common area assigned thereto are or are not in violation of the declaration, bylaws, rules and regulations, architectural guidelines and articles of incorporation, if any, of the association;","10. A statement setting forth any restriction, limitation, or prohibition on the right of a lot owner to place a sign on the owner's lot advertising the lot for sale;","11. A statement setting forth any restriction, limitation, or prohibition on the right of a lot owner to display any flag on the owner's lot, including but not limited to reasonable restrictions as to the size, place, and manner of placement or display of such flag and the installation of any flagpole or similar structure necessary to display such flag;","12. A statement setting forth any restriction, limitation, or prohibition on the right of a lot owner to install or use solar energy collection devices on the owner's property;","13. A copy of the current declaration, the association's articles of incorporation and bylaws, and any rules and regulations or architectural guidelines adopted by the association;","14. A copy of any approved minutes of the board of directors and association meetings for the six calendar months preceding the request for the disclosure packet;","15. A copy of the notice given to the lot owner by the association of any current or pending rule or architectural violation;","16. A copy of the fully completed form developed by the Common Interest Community Board pursuant to \xA7 54.1-2350;","17. Certification that the association has filed with the Common Interest Community Board the annual report required by \xA7 55-516.1, which certification shall indicate the filing number assigned by the Common Interest Community Board, and the expiration date of such filing; and","18. A statement indicating any known project approvals currently in effect issued by secondary mortgage market agencies.","B. Failure to receive copies of an association disclosure packet shall not excuse any failure to comply with the provisions of the declaration, articles of incorporation, bylaws, or rules or regulations.","C. The disclosure packet shall be delivered in accordance with the written request and instructions of the seller or the seller's authorized agent, including whether the disclosure packet shall be delivered electronically or in hard copy and shall specify the complete contact information for the parties to whom the disclosure packet shall be delivered. The disclosure packet required by this section, shall not, in and of itself, be deemed a security within the meaning of \xA7 13.1-501.","D. The seller or the seller's authorized agent may request that the disclosure packet be provided in hard copy or in electronic form. An association or common interest community manager may provide the disclosure packet electronically; however, the seller or the seller's authorized agent shall have the right to request that the association disclosure packet be provided in hard copy. The seller or the seller's authorized agent shall continue to have the right to request a hard copy of the disclosure packet in person at the principal place of business of the association. If the seller or the seller's authorized agent requests that the disclosure packet be provided in electronic format, neither the association nor its common interest community manager may require the seller or the seller's authorized agent to pay any fees to use the provider's electronic network or system. The disclosure packet shall not be delivered in hard copy if the requester has requested delivery of such disclosure packet electronically. If the disclosure packet is provided electronically by a website link, the preparer shall not cause the website link to expire within the subsequent 90-day period. The preparer shall not charge another fee during the subsequent 12-month period, except that the preparer may charge an update fee for a financial update or for an inspection as provided in \xA7 55-509.6. If the seller or the seller's authorized agent asks that the disclosure packet be provided in electronic format, the seller or the seller's authorized agent may request that an electronic copy be provided to each of the following named in the request: the seller, the seller's authorized agent, the purchaser, the purchaser's authorized agent, and not more than one other person designated by the requester. If so requested, the property owners' association or its common interest community manager may require the seller or the seller's authorized agent to pay the fee specified in \xA7 55-509.6. Regardless of whether the disclosure packet is delivered in paper form or electronically, the preparer of the disclosure packet shall provide such disclosure packet directly to the persons designated by the requester to the addresses or, if applicable, the email addresses provided by the requester."]},{title:"\xA7 55-509.6. Fees for disclosure packet; professionally managed associations.",text:["A. A professionally managed association or its common interest community manager may charge certain fees as authorized by this section for the inspection of the property, the preparation and issuance of the disclosure packet required by \xA7 55-509.5, and for such other services as set out in this section. The seller or the seller's authorized agent shall specify in writing whether the disclosure packet shall be delivered electronically or in hard copy, at the option of the seller or the seller's authorized agent, and shall specify the complete contact information for the parties to whom the disclosure packet shall be delivered.","B. A reasonable fee may be charged by the preparer as follows for:","1. The inspection of the exterior of the dwelling unit and the lot, as authorized in the declaration and as required to prepare the association disclosure packet, a fee not to exceed $100;","2. The preparation and delivery of the disclosure packet in (i) paper format, a fee not to exceed $150 for no more than two hard copies or (ii) electronic format, a fee not to exceed a total of $125 for an electronic copy to each of the following named in the request: the seller, the seller's authorized agent, the purchaser, the purchaser's authorized agent, and not more than one other person designated by the requester. The preparer of the disclosure packet shall provide the disclosure packet directly to the designated persons. Only one fee shall be charged for the preparation and delivery of the disclosure packet;","3. At the option of the seller or the seller's authorized agent, with the consent of the association or the common interest community manager, expediting the inspection, preparation and delivery of the disclosure packet, an additional expedite fee not to exceed $50;","4. At the option of the seller or the seller's authorized agent, an additional hard copy of the disclosure packet, a fee not to exceed $25 per hard copy;","5. At the option of the seller or the seller's authorized agent, a fee not to exceed an amount equal to the actual cost paid to a third-party commercial delivery service for hand delivery or overnight delivery of the association disclosure packet; and","6. A post-closing fee to the purchaser of the property, collected at settlement, for the purpose of establishing the purchaser as the owner of the property in the records of the association, a fee not to exceed $50.","Except as otherwise provided in subsection E, neither the association nor its common interest community manager shall require cash, check, certified funds or credit card payments at the time the request for the disclosure packet is made. The disclosure packet shall state that all fees and costs for the disclosure packet shall be the personal obligation of the lot owner and shall be an assessment against the lot and collectible as any other assessment in accordance with the provisions of the declaration and \xA7 55-516, if not paid at settlement or within 60 days of the delivery of the disclosure packet, whichever occurs first.","For purposes of this section, an expedite fee shall only be charged if the inspection and preparation of delivery of the disclosure packet are completed within five business days of the request for a disclosure packet.","C. No fees other than those specified in this section, and as limited by this section, shall be charged by the association or its common interest community manager for compliance with the duties and responsibilities of the association under this chapter. No additional fee shall be charged for access to the association's or common interest community manager's website. The association or its common interest community manager shall publish and make available in paper or electronic format, or both, a schedule of the applicable fees so the seller or the seller's authorized agent will know such fees at the time of requesting the packet.","D. Any fees charged pursuant to this section shall be collected at the time of settlement on the sale of the lot and shall be due and payable out of the settlement proceeds in accordance with this section. The settlement agent shall escrow a sum sufficient to pay such costs of the seller at settlement. The seller shall be responsible for all costs associated with the preparation and delivery of the association disclosure packet, except for the costs of any disclosure packet update or financial update, which costs shall be the responsibility of the requester, payable at settlement. Neither the association nor its common interest community manager shall require cash, check, certified funds, or credit card payments at the time of the request is made for the association disclosure packet.","E. If settlement does not occur within 60 days of the delivery of the disclosure packet, or funds are not collected at settlement and disbursed to the association or the common interest community manager, all fees, including those costs that would have otherwise been the responsibility of the purchaser or settlement agent, shall be (i) assessed within one year after delivery of the disclosure packet against the lot owner, (ii) the personal obligation of the lot owner, and (iii) an assessment against the lot and collectible as any other assessment in accordance with the provisions of the declaration and \xA7 55-516. The seller may pay the association by cash, check, certified funds, or credit card, if credit card payment is an option offered by the association. The association shall pay the common interest community manager the amount due from the lot owner within 30 days after invoice.","F. The maximum allowable fees charged in accordance with this section shall adjust every five years, as of January 1 of that year, in an amount equal to the annual increases for that five-year period in the United States Average Consumer Price Index for all items, all urban consumers (CPI-U), as published by the Bureau of Labor Statistics of the U.S. Department of Labor.","G. If an association disclosure packet has been issued for a lot within the preceding 12-month period, a person specified in the written instructions of the seller or the seller's authorized agent, including the seller or the seller's authorized agent, or the purchaser or his authorized agent may request a disclosure packet update. The requester shall specify whether the disclosure packet update shall be delivered electronically or in hard copy, and shall specify the complete contact information of the parties to whom the update shall be delivered. The disclosure packet update shall be delivered within 10 days of the written request.","H. The settlement agent may request a financial update. The requester shall specify whether the financial update shall be delivered electronically or in hard copy, and shall specify the complete contact information of the parties to whom the update shall be delivered. The financial update shall be delivered within three business days of the written request.","I. A reasonable fee for the disclosure packet update or financial update may be charged by the preparer not to exceed $50. At the option of the purchaser or the purchaser's authorized agent, the requester may request that the association or the common interest community manager perform an additional inspection of the exterior of the dwelling unit and the lot, as authorized in the declaration, for a fee not to exceed $100. Any fees charged for the specified update shall be collected at the time settlement occurs on the sale of the property. The settlement agent shall escrow a sum sufficient to pay such costs of the seller at settlement. Neither the association nor its common interest community manager, if any, shall require cash, check, certified funds, or credit card payments at the time the request is made for the disclosure packet update. The requester may request that the specified update be provided in hard copy or in electronic form.","J. No association or common interest community manager may require the requester to request the specified update electronically. The seller or the seller's authorized agent shall continue to have the right to request a hard copy of the specified update in person at the principal place of business of the association. If the requester asks that the specified update be provided in electronic format, neither the association nor its common interest community manager may require the requester to pay any fees to use the provider's electronic network or system. A copy of the specified update shall be provided to the seller or the seller's authorized agent.","K. When an association disclosure packet has been delivered as required by \xA7 55-509.5, the association shall, as to the purchaser, be bound by the statements set forth therein as to the status of the assessment account and the status of the lot with respect to any violation of the declaration, bylaws, rules and regulations, architectural guidelines and articles of incorporation, if any, of the association as of the date of the statement unless the purchaser had actual knowledge that the contents of the disclosure packet were in error.","L. If the association or its common interest community manager has been requested in writing to furnish the association disclosure packet required by \xA7 55-509.5, failure to provide the association disclosure packet substantially in the form provided in this section shall be deemed a waiver of any claim for delinquent assessments or of any violation of the declaration, bylaws, rules and regulations, or architectural guidelines existing as of the date of the request with respect to the subject lot. The preparer of the association disclosure packet shall be liable to the seller in an amount equal to the actual damages sustained by the seller in an amount not to exceed $1,000. The purchaser shall nevertheless be obligated to abide by the declaration, bylaws, rules and regulations, and architectural guidelines of the association as to all matters arising after the date of the settlement of the sale.","M. The Common Interest Community Board may assess a monetary penalty for failure to deliver the association disclosure packet within 14 days against any (i) property owners' association pursuant to \xA7 54.1-2351 or (ii) common interest community manager pursuant to \xA7 54.1-2349 and regulations promulgated thereto, and may issue a cease and desist order pursuant to \xA7 54.1-2349 or 54.1-2352, as applicable.","N. No association may collect fees authorized by this section unless the association (i) is registered with the Common Interest Community Board, (ii) is current in filing the most recent annual report with the Common Interest Community Board, (iii) is current in paying the annual payment to the Common Interest Community Board pursuant to \xA7 55-516.1 and any assessment made by the Common Interest Community Board pursuant to \xA7 55-530.1, and (iv) provides the disclosure packet electronically if so requested by the requester."]},{title:"\xA7 55-509.7. Fees for disclosure packets; associations not professionally managed.",text:["A. An association that is not professionally managed may charge a fee for the preparation and issuance of the association disclosure packet required by \xA7 55-509.5. Any fee shall reflect the actual cost of the preparation of the association disclosure packet, but shall not exceed $0.10 per page of copying costs or a total of $100 for all costs incurred in preparing the association disclosure packet. The seller or his authorized agent shall specify whether the association disclosure packet shall be delivered electronically or in hard copy and shall specify the complete contact information of the parties to whom the disclosure packet shall be delivered. If the seller or his authorized agent specifies that delivery shall be made to the purchaser or his authorized agent, the preparer shall provide the disclosure packet directly to the designated persons, at the same time it is delivered to the seller or his authorized agent. The association shall advise the requestor if electronic delivery of the disclosure packet or the disclosure packet update or financial update is not available, if electronic delivery has been requested by the seller or his authorized agent.","B. At the option of the seller or the seller's authorized agent, with the consent of the association, a reasonable fee may be charged for (i) expediting the inspection, preparation and delivery of the disclosure packet, if completed within five business days of the request, not to exceed $50; (ii) an additional hard copy of the disclosure packet not to exceed $25 per hard copy; and (iii) third-party commercial delivery service for hand delivery or overnight delivery of the association disclosure packet not to exceed an amount equal to the actual cost paid.","C. No fees other than those specified in this section shall be charged by the association for compliance with duties and responsibilities under this section. Any fees charged pursuant to this section shall be collected at the time of delivery of the disclosure packet. If unpaid, any such fees shall be an assessment against the lot and collectible as any other assessment in accordance with the provisions of the declaration and \xA7 55-516. The seller may pay the association by cash, check, certified funds, or credit card, if credit card payment is an option offered by the association.","D. If an association disclosure packet has been issued for a lot within the preceding 12-month period, a person specified in the written instructions of the seller or his authorized agent, including the seller or his authorized agent, or the purchaser or his authorized agent may request a disclosure packet update. The requestor shall specify whether the disclosure packet update shall be delivered electronically or in hard copy and shall specify the complete contact information of the parties to whom the specified update shall be delivered. The disclosure packet update shall be delivered within 10 days of the written request therefor.","E. The settlement agent may request a financial update. The requestor shall specify whether the financial update shall be delivered electronically or in hard copy, and shall specify the complete contact information of the parties to whom the update shall be delivered. The financial update shall be delivered within three business days of the written request therefor.","F. A reasonable fee for the disclosure packet update or a financial update may be charged by the preparer not to exceed $50. At the option of the purchaser or his authorized agent, the requestor may request that the association perform an additional inspection of the exterior of the dwelling unit and the lot, as authorized in the declaration, for a fee not to exceed $50. Any fees charged for the specified update shall be collected at the time of delivery of the update. The association shall not require cash, check, certified funds, or credit card payments at the time the request is made for the disclosure packet update. The requestor may request that the specified update be provided in hard copy or in electronic form.","G. No association may require the requestor to request the specified update electronically. The seller or his authorized agent shall continue to have the right to request a hard copy of the specified update in person at the principal place of business of the association. If the requestor asks that the specified update be provided in electronic format, the association shall not require the requester to pay any fees to use the provider's electronic network or system. If the requestor asks that the specified update be provided in electronic format, the requestor may designate no more than two additional recipients to receive the specified update in electronic format at no additional charge. A copy of the specified update shall be provided to the seller or his authorized agent.","H. When a disclosure packet has been delivered as required by \xA7 55-509.5, the association shall, as to the purchaser, be bound by the statements set forth therein as to the status of the assessment account and the status of the lot with respect to any violation of the declaration, bylaws, rules and regulations, architectural guidelines and articles of incorporation, if any, of the association as of the date of the statement unless the purchaser had actual knowledge that the contents of the disclosure packet were in error.","I. If the association has been requested to furnish the association disclosure packet required by this section, failure to provide the association disclosure packet substantially in the form provided in this section shall be deemed a waiver of any claim for delinquent assessments or of any violation of the declaration, bylaws, rules and regulations, or architectural guidelines existing as of the date of the request with respect to the subject lot. The association shall be liable to the seller in an amount equal to the actual damages sustained by the seller in an amount not to exceed $500. The purchaser shall nevertheless be obligated to abide by the declaration, bylaws, rules and regulations, and architectural guidelines of the association as to all matters arising after the date of the settlement of the sale.","J. No association may collect fees authorized by this section unless the association (i) is registered with the Common Interest Community Board, (ii) is current in filing the most recent annual report with the Common Interest Community Board, and (iii) is current in paying the annual payment to the Common Interest Community Board pursuant to \xA7 55-516.1 and any assessment made by the Common Interest Community Board pursuant to \xA7 55-530.1.","K. An association that is not professionally managed may charge and collect fees for inspection of the property, the preparation and issuance of an association disclosure packet, and such other services as set out in \xA7 55-509.6, provided that the association provides the disclosure packet electronically if so requested by the requester and otherwise complies with \xA7 55-509.6."]}];

