var condoText2=[{title:"\xA7 55-79.45. How condominium may be created.",text:["No condominium shall come into existence except by the recordation of condominium instruments pursuant to the provisions of this chapter. No condominium instruments shall be recorded unless all units located or to be located on any portion of the submitted land, other than within the boundaries of any convertible lands, are depicted on plats and plans that comply with the provisions of subsections A and B of \xA7 55-79.58. The foreclosure of any mortgage, deed of trust, or other lien shall not be deemed, ex proprio vigore, to terminate the condominium."]},{title:"\xA7 55-79.46. Release of liens.",text:["A. At the time of the conveyance to the first purchaser of each condominium unit following the recordation of the declaration, every mortgage, deed of trust, any other perfected lien, or any mechanics' or materialmen's liens, affecting all of the condominium or a greater portion thereof than the condominium unit conveyed, shall be paid and satisfied of record, or the declarant shall forthwith have the said condominium unit released of record from all such liens not so paid and satisfied. The provisions of this subsection shall not apply, however, to any withdrawable land in a contractable condominium, nor shall any provision of this subsection be construed to prohibit the unit owners' association from mortgaging or causing a deed of trust to be placed on any portion of the condominium within which no units are located, so long as any time limit specified pursuant to \xA7 55-79.74 has expired, and so long as the bylaws authorize the same. This subsection shall not apply to any lien on more than one condominium unit in a condominium in which all units are restricted to nonresidential use and in which all unit owners whose condominium units will be subject to such lien expressly agree to assume or take subject thereto.","B. In the event that any lien, other than a deed of trust or mortgage, becomes effective against two or more condominium units subsequent to the creation of the condominium, any unit owner may remove his condominium unit from that lien by payment of the amount attributable to his condominium unit. Such amount shall be computed by reference to the liability for common expenses appertaining to that condominium unit pursuant to subsection D of \xA7 55-79.83. Subsequent to such payment, discharge or other satisfaction, the unit owner of that condominium unit shall be entitled to have that lien released as to his condominium unit in accordance with the provisions of \xA7 55-66.4, and the unit owners' association shall not assess, or have a valid lien against, that condominium unit for any portion of the common expenses incurred in connection with that lien, notwithstanding anything to the contrary in \xA7\xA7 55-79.83 and 55-79.84."]},{title:"\xA7 55-79.47. Description of condominium units.",text:["After the creation of the condominium, no description of a condominium unit shall be deemed vague, uncertain, or otherwise insufficient or infirm which sets forth the identifying number of that unit, the name of the condominium, the name of the city or county wherein the condominium is situated, and either the deed book and page number where the first page of the declaration is recorded or else the document number assigned to the declaration by the clerk. Any such description shall be deemed to include the undivided interest in the common elements appertaining to such unit even if such interest is not defined or referred to therein."]},{title:"\xA7 55-79.48. Execution of condominium instruments.",text:["The declaration and bylaws, and any amendments to either made pursuant to \xA7 55-79.71 shall be duly executed by or on behalf of all of the owners and lessees of the submitted land. But the phrase \"owners and lessees\" in the preceding sentence and in \xA7 55-79.63 does not include, in their capacity as such, any mortgagee, any trustee or beneficiary under a deed of trust, any other lien holder, any person having an equitable interest under any contract for the sale and/or lease of a condominium unit, any lessee whose leasehold interest does not extend to any portion of the common elements, any person whose land is subject to an easement included in the condominium or, in the case of a leasehold condominium subject to any lease or leases executed before July 1, 1962, any lessor of the submitted land who is not a declarant."]},{title:"\xA7 55-79.49. Recordation of condominium instruments.",text:["All amendments and certifications of condominium instruments shall set forth the name of the city or county in which the condominium is located, and the deed book and page number where the first page of the declaration is recorded. All condominium instruments and all amendments and certifications thereof shall be recorded in every city and county wherein any portion of the condominium is located. The condominium instruments, amendments and certifications shall set forth the name of the condominium and either the deed book and page number where the first page of the declaration is recorded or the document number assigned to the declaration by the clerk. Wherever the phrase \"city or county\" appears in this chapter, the disjunctive shall be deemed to include the conjunctive and the singular shall be deemed to include the plural."]},{title:"\xA7 55-79.50. Construction of condominium instruments.",text:["Except to the extent otherwise provided by the condominium instruments:","(a) The terms defined in \xA7 55-79.41 shall be deemed to have the meanings therein specified wherever they appear in the condominium instruments unless the context otherwise requires.","(b) To the extent that walls, floors and/or ceilings are designated as the boundaries of the units or of any specified units, all lath, wallboard, plasterboard, plaster, paneling, tiles, wallpaper, paint, finished flooring and any other materials constituting any part of the finished surfaces thereof, shall be deemed a part of such units, while all other portions of such walls, floors and/or ceilings shall be deemed a part of the common elements.","(c) If any chutes, flues, ducts, conduits, wires, bearing walls, bearing columns or any other apparatus lies partially within and partially outside of the designated boundaries of a unit, any portions thereof serving only that unit shall be deemed a part of that unit, while any portions thereof serving more than one unit or any portion of the common elements shall be deemed a part of the common elements.","(d) Subject to the provisions of subsection (c) hereof, all space, interior partitions and other fixtures and improvements within the boundaries of a unit shall be deemed a part of that unit.","(e) Any shutters, awnings, doors, windows, window boxes, doorsteps, porches, balconies, patios and any other apparatus designed to serve a single unit, but located outside the boundaries thereof, shall be deemed a limited common element appertaining to that unit exclusively; provided that if a single unit's electrical master switch is located outside the designated boundaries of the unit, the switch and its cover shall be deemed a part of the common elements."]},{title:"\xA7 55-79.51. Complementarity of condominium instruments; controlling construction.",text:["The condominium instruments shall be construed together and shall be deemed to incorporate one another to the extent that any requirement of this chapter as to the content of one shall be deemed satisfied if the deficiency can be cured by reference to any of the others. In the event of any conflict between the condominium instruments, the declaration shall control; but particular provisions shall control more general provisions, except that a construction conformable with the statute shall in all cases control over any construction inconsistent therewith."]},{title:"\xA7 55-79.52. Validity of condominium instruments; discrimination prohibited.",text:["A. All provisions of the condominium instruments shall be deemed severable, and any unlawful provision thereof shall be void.","B. No provision of the condominium instruments shall be deemed void by reason of the rule against perpetuities.","C. No restraint on alienation shall discriminate or be used to discriminate on any basis prohibited under the Virginia Fair Housing Law (\xA7 36-96.1 et seq.).","D. Subject to the provisions of subsection C, the rule of property law known as the rule restricting unreasonable restraints on alienation shall not be applied to defeat any provision of the condominium instruments restraining the alienation of condominium units other than such units as may be restricted to residential use only."]},{title:"\xA7 55-79.53. Compliance with condominium instruments.",text:["A. The declarant, every unit owner, and all those entitled to occupy a unit shall comply with all lawful provisions of this chapter and all provisions of the condominium instruments. Any lack of such compliance shall be grounds for an action or suit to recover sums due, for damages or injunctive relief, or for any other remedy available at law or in equity, maintainable by the unit owners' association, or by its executive organ or any managing agent on behalf of such association, or, in any proper case, by one or more aggrieved unit owners on their own behalf or as a class action. A unit owners' association shall have standing to sue in its own name for any claims or actions related to the common elements as provided in subsection B of \xA7 55-79.80. Except as provided in subsection B, the prevailing party shall be entitled to recover reasonable attorney fees, costs expended in the matter, and interest on the judgment as provided in \xA7 8.01-382. This section shall not preclude an action against the unit owners' association and authorizes the recovery, by the prevailing party in any such action, of reasonable attorney fees, costs expended in the matter, and interest on the judgment as provided in \xA7 8.01-382 in such actions.","B. In actions against a unit owner for nonpayment of assessments in which the unit owner has failed to pay assessments levied by the unit owners' association on more than one unit or such unit owner has had legal actions taken against him for nonpayment of any prior assessment and the prevailing party is the association or its executive organ or any managing agent on behalf of the association, the prevailing party shall be awarded reasonable attorney fees, costs expended in the matter, and interest on the judgment as provided in subsection A, even if the proceeding is settled prior to judgment. The delinquent unit owner shall be personally responsible for reasonable attorney fees and costs expended in the matter by the unit owners' association, whether any judicial proceedings are filed.","C. The condominium instruments may provide for arbitration of disputes or other means of alternative dispute resolution. Any such arbitration held in accordance with this subsection shall be consistent with the provisions of this chapter and Chapter 21 (\xA7 8.01-577 et seq.) of Title 8.01. The place of any such arbitration or alternative dispute resolution shall be in the county or city in which the condominium is located, or as mutually agreed by the parties."]},{title:"\xA7 55-79.54. Contents of declaration.",text:["(a) The declaration for every condominium shall contain the following:","(1) The name of the condominium, which name shall include the word \"condominium\" or be followed by the words \"a condominium.\"","(2) The name of the city or county in which the condominium is located.","(3) A legal description by metes and bounds of the land submitted to this chapter.","(4) A description or delineation of the boundaries of the units, including the horizontal (upper and lower) boundaries, if any, as well as the vertical (lateral or perimetric) boundaries.","(5) A description or delineation of any limited common elements, other than those which are limited common elements by virtue of subsection (e) of \xA7 55-79.50, showing or designating the unit or units to which each is assigned.","(6) A description or delineation of all common elements not within the boundaries of any convertible lands which may subsequently be assigned as limited common elements, together with a statement that (i) they may be so assigned and a description of the method whereby any such assignments shall be made in accordance with the provisions of \xA7 55-79.57 or (ii) once assigned, the conditions under which they may be unassigned and converted to common elements in accordance with \xA7 55-79.57.","(7) The allocation to each unit of an undivided interest in the common elements in accordance with the provisions of \xA7 55-79.55.","(7a) A statement of the extent of the declarant's obligation to complete improvements labeled \"(NOT YET COMPLETED)\" or to begin and complete improvements labeled \"(NOT YET BEGUN)\" on plats recorded pursuant to the requirements of this chapter. Such statement shall be specific as to the type and quality of materials to be used, the size or capacity of the improvements, when material, and the time by which such improvements shall be completed.","(8) Such other matters as the declarant deems appropriate.","(b) If the condominium contains any convertible land, the declaration shall also contain the following:","(1) A legal description by metes and bounds of each convertible land within the condominium.","(2) A statement of the maximum number of units that may be created within each such convertible land.","(3) A statement, with respect to each such convertible land, of the maximum percentage of the aggregate land and floor area of all units that may be created therein that may be occupied by units not restricted exclusively to residential use.","(4) A statement of the extent to which any structure erected on any convertible land will be compatible with structures on other portions of the submitted land in terms of quality of construction, the principal materials to be used, and architectural style.","(5) A description of all other improvements that may be made on each convertible land within the condominium.","(6) A statement that any units created within each convertible land will be substantially identical to the units on other portions of the submitted land, or a statement describing in detail what other types of units may be created therein.","(7) A description of the declarant's reserved right, if any, to create limited common elements within any convertible land, and/or to designate common elements therein which may subsequently be assigned as limited common elements, in terms of the types, sizes, and maximum number of such elements within each such convertible land.","Provided, that plats and plans may be recorded with the declaration and identified therein to supplement information furnished pursuant to items (1), (4), (5), (6), and (7), and that item (3) need not be complied with if none of the units on other portions of the submitted land are restricted exclusively to residential use.","(c) If the condominium is an expandable condominium, the declaration shall also contain the following:","(1) The explicit reservation of an option to expand the condominium.","(2) A statement of any limitations on that option, including, without limitation, a statement as to whether the consent of any unit owners shall be required, and if so, a statement as to the method whereby such consent shall be ascertained; or a statement that there are no such limitations.","(3) A time limit, not exceeding 10 years from the recording of the declaration, upon which the option to expand the condominium shall expire, together with a statement of the circumstances, if any, which will terminate that option prior to the expiration of the time limit so specified. After the expiration of any period of declarant control reserved pursuant to subsection A of \xA7 55-79.74, such time limit may be extended by an amendment to the declaration made pursuant to \xA7 55-79.71.","(4) A legal description by metes and bounds of all land that may be added to the condominium, henceforth referred to as \"additional land.\"","(5) A statement as to whether, if any of the additional land is added to the condominium, all of it or any particular portion of it must be added, and if not, a statement of any limitations as to what portions may be added or a statement that there are no such limitations.","(6) A statement as to whether portions of the additional land may be added to the condominium at different times, together with any limitations fixing the boundaries of those portions by legal descriptions setting forth the metes and bounds thereof and/or regulating the order in which they may be added to the condominium.","(7) A statement of any limitations as to the locations of any improvements that may be made on any portions of the additional land added to the condominium, or a statement that no assurances are made in that regard.","(8) A statement of the maximum number of units that may be created on the additional land. If portions of the additional land may be added to the condominium and the boundaries of those portions are fixed in accordance with item (6), the declaration shall also state the maximum number of units that may be created on each such portion added to the condominium. If portions of the additional land may be added to the condominium and the boundaries of those portions are not fixed in accordance with item (6), then the declaration shall also state the maximum number of units per acre that may be created on any such portion added to the condominium.","(9) A statement, with respect to the additional land and to any portion or portions thereof that may be added to the condominium, of the maximum percentage of the aggregate land and floor area of all units that may be created thereon that may be occupied by units not restricted exclusively to residential use.","(10) A statement of the extent to which any structures erected on any portion of the additional land added to the condominium will be compatible with structures on the submitted land in terms of quality of construction, the principal materials to be used, and architectural style, or a statement that no assurances are made in those regards.","(11) A description of all other improvements that will be made on any portion of the additional land added to the condominium, or a statement of any limitations as to what other improvements may be made thereon, or a statement that no assurances are made in that regard.","(12) A statement that any units created on any portion of the additional land added to the condominium will be substantially identical to the units on the submitted land, or a statement of any limitations as to what types of units may be created thereon, or a statement that no assurances are made in that regard.","(13) A description of the declarant's reserved right, if any, to create limited common elements within any portion of the additional land added to the condominium, and/or to designate common elements therein which may subsequently be assigned as limited common elements, in terms of the types, sizes, and maximum number of such elements within each such portion, or a statement that no assurances are made in those regards.","Provided, that plats and plans may be recorded with the declaration and identified therein to supplement information furnished pursuant to items (4), (5), (6), (7), (10), (11), (12), and (13), and that item (9) need not be complied with if none of the units on the submitted land are restricted exclusively to residential use.","(d) If the condominium is a contractable condominium, the declaration shall also contain the following:","(1) The explicit reservation of an option to contract the condominium.","(2) A statement of any limitations on that option, including, without limitation, a statement as to whether the consent of any unit owners shall be required, and if so, a statement as to the method whereby such consent shall be ascertained; or a statement that there are no such limitations.","(3) A time limit, not exceeding 10 years from the recording of the declaration, upon which the option to contract the condominium shall expire, together with a statement of the circumstances, if any, which will terminate that option prior to the expiration of the time limit so specified.","(4) A legal description by metes and bounds of all land that may be withdrawn from the condominium, henceforth referred to as \"withdrawable land.\"","(5) A statement as to whether portions of the withdrawable land may be withdrawn from the condominium at different times, together with any limitations fixing the boundaries of those portions by legal descriptions setting forth the metes and bounds thereof and/or regulating the order in which they may be withdrawn from the condominium.","(6) A legal description by metes and bounds of all of the submitted land to which the option to contract the condominium does not extend.","Provided, that plats may be recorded with the declaration and identified therein to supplement information furnished pursuant to items (4), (5), and (6), and that item (6) shall not be construed in derogation of any right the declarant may have to terminate the condominium in accordance with the provisions of \xA7 55-79.72:1.","(e) If the condominium is a leasehold condominium, then with respect to any ground lease or other leases the expiration or termination of which will or may terminate or contract the condominium, the declaration shall set forth the city or county wherein the same are recorded and the deed book and page number where the first page of each such lease is recorded; and the declaration shall also contain the following:","(1) The date upon which each such lease is due to expire.","(2) A statement as to whether any land and/or improvements will be owned by the unit owners in fee simple, and if so, either (a) a description of the same, including without limitation a legal description by metes and bounds of any such land, or (b) a statement of any rights the unit owners shall have to remove such improvements within a reasonable time after the expiration or termination of the lease or leases involved, or a statement that they shall have no such rights.","(3) A statement of the rights the unit owners shall have to redeem the reversion or any of the reversions, or a statement that they shall have no such rights.","Provided, that after the recording of the declaration, no lessor who executed the same, and no successor in interest to such lessor, shall have any right or power to terminate any part of the leasehold interest of any unit owner who makes timely payment of his share of the rent to the person or persons designated in the declaration for the receipt of such rent and who otherwise complies with all covenants which, if violated, would entitle the lessor to terminate the lease. Acquisition or reacquisition of such a leasehold interest by the owner of the reversion or remainder shall not cause a merger of the leasehold and fee simple interests unless all leasehold interests in the condominium are thus acquired or reacquired.","(f) Wherever this section requires a legal description by metes and bounds of land that is submitted to this chapter or that may be added to or withdrawn from the condominium, such requirement shall be deemed satisfied by any legally sufficient description and shall be deemed to require a legally sufficient description of any easements that are submitted to this chapter or that may be added to or withdrawn from the condominium, as the case may be. In the case of each such easement, the declaration shall contain the following:","(1) A description of the permitted use or uses.","(2) If less than all of those entitled to the use of all of the units may utilize such easement, a statement of the relevant restrictions and limitations on utilization.","(3) If any persons other than those entitled to the use of the units may utilize such easement, a statement of the rights of others to utilization of the same.","(g) Wherever this section requires a legal description by metes and bounds of land that is submitted to this chapter or that may be added to or withdrawn from the condominium, an added requirement shall be a separate legally sufficient description of all lands in which the unit owners shall or may be tenants in common or joint tenants with any other persons, and a separate legally sufficient description of all lands in which the unit owners shall or may be life tenants. No units shall be situated on any such lands, however, and the declaration shall describe the nature of the unit owners' estate therein. No such lands shall be shown on the same plat or plats showing other portions of the condominium, but shall be shown instead on separate plats."]},{title:"\xA7 55-79.55. Allocation of interests in the common elements.",text:["(a) The declaration may allocate to each unit depicted on plats and plans that comply with subsections A and B of \xA7 55-79.58 an undivided interest in the common elements proportionate to either the size or par value of each unit.","(b) Otherwise, the declaration shall allocate to each such unit an equal undivided interest in the common elements, subject to the following exception: Each convertible space so depicted shall be allocated an undivided interest in the common elements proportionate to the size of each such space, vis-a-vis the aggregate size of all units so depicted, while the remaining undivided interest in the common elements shall be allocated equally to the other units so depicted.","(c) The undivided interests in the common elements allocated in accordance with subsection (a) or (b) hereof shall add up to 1 if stated as fractions or 100% if stated as percentages.","(d) If, in accordance with subsection (a) or (b) hereof, an equal undivided interest in the common elements is allocated to each unit, the declaration may simply state that fact and need not express the fraction or percentage so allocated.","(e) Otherwise, the undivided interest allocated to each unit in accordance with subsection (a) or (b) hereof shall be reflected by a table in the declaration, or by an exhibit or schedule accompanying the declaration and recorded simultaneously therewith, containing three columns. The first column shall identify the units, listing them serially or grouping them together in the case of units to which identical undivided interests are allocated. Corresponding figures in the second and third columns shall set forth the respective areas or par values of those units and the fraction or percentage of undivided interest in the common elements allocated thereto.","(f) Except to the extent otherwise expressly provided by this chapter, the undivided interest in the common elements allocated to any unit shall not be altered, and any purported transfer, encumbrance, or other disposition of that interest without the unit to which it appertains shall be void.","(g) The common elements shall not be subject to any suit for partition until and unless the condominium is terminated."]},{title:"\xA7 55-79.56. Reallocation of interests in common elements.",text:["(a) If a condominium contains any convertible land or is an expandable condominium, then the declaration shall not allocate undivided interests in the common elements on the basis of par value unless the declaration:","(1) Prohibits the creation of any units not substantially identical to the units depicted on the plats and plans recorded pursuant to subsections A and B of \xA7 55-79.58, or","(2) Prohibits the creation of any units not described pursuant to subdivision (b) (6) of \xA7 55-79.54 (in the case of convertible lands) and subdivision (c) (12) of \xA7 55-79.54 (in the case of additional land), and contains from the outset a statement of the par value that shall be assigned to every such unit that may be created.","(b) Interests in the common elements shall not be allocated to any units to be created within any convertible land or within any additional land until plats and plans depicting the same are recorded pursuant to subsection C of \xA7 55-79.58. But simultaneously with the recording of such plats and plans the declarant shall execute and record an amendment to the declaration reallocating undivided interests in the common elements so that the units depicted on such plats and plans shall be allocated undivided interests in the common elements on the same basis as the units depicted on the plats and plans recorded simultaneously with the declaration pursuant to subsections A and B of \xA7 55-79.58.","(c) If all of a convertible space is converted into common elements, including without limitation limited common elements, then the undivided interest in the common elements appertaining to such space shall thenceforth appertain to the remaining units, being allocated among them in proportion to their undivided interests in the common elements. The principal officer of the unit owners' association, or such other officer or officers as the condominium instruments may specify, shall forthwith prepare, execute, and record an amendment to the declaration reflecting the reallocation of undivided interests produced thereby.","(d) In the case of a leasehold condominium, if the expiration or termination of any lease causes a contraction of the condominium which reduces the number of units, then the undivided interest in the common elements appertaining to any units thereby withdrawn from the condominium shall thenceforth appertain to the remaining units, being allocated among them in proportion to their undivided interests in the common elements. The principal officer of the unit owners' association, or such other officer or officers as the condominium instruments may specify, shall forthwith prepare, execute, and record an amendment to the declaration reflecting the reallocation of undivided interests produced thereby."]},{title:"\xA7 55-79.57. Assignments of limited common elements; conversion to common element.",text:["A. All assignments and reassignments of limited common elements shall be reflected by the condominium instruments. No limited common element shall be assigned or reassigned except in accordance with the provisions of this chapter. No amendment to any condominium instrument shall alter any rights or obligations with respect to any limited common elements without the consent of all unit owners adversely affected thereby as evidenced by their execution of such amendment, except to the extent that the condominium instruments expressly provided otherwise prior to the first assignment of that limited common element.","B. Unless expressly prohibited by the condominium instruments, a limited common element may be reassigned or converted to a common element upon written application of the unit owners concerned to the principal officer of the unit owners' association, or to such other officer or officers as the condominium instruments may specify. The officer or officers to whom such application is duly made shall forthwith prepare and execute an amendment to the declaration reassigning all rights and obligations with respect to the limited common element involved. Such amendment shall be executed by the unit owner or unit owners of the unit or units concerned and recorded by an officer of the unit owners' association or his agent following payment by the unit owner or unit owners of the unit or units concerned of all reasonable costs for the preparation, acknowledgment and recordation thereof. The amendment shall become effective when recorded.","C. A common element not previously assigned as a limited common element shall be so assigned only in pursuance of subdivision (a) (6) of \xA7 55-79.54. The amendment to the declaration making such an assignment shall be prepared and executed by the declarant, the principal officer of the unit owners' association, or by such other officer or officers as the condominium instruments may specify. Such amendment shall be recorded by the declarant or his agent, without charge to any unit owner, or by an officer of the unit owners' association or his agent following payment by the unit owner or unit owners of the unit or units concerned of all reasonable costs for the preparation, acknowledgment and recordation thereof. The amendment shall become effective when recorded, and the recordation thereof shall be conclusive evidence that the method prescribed pursuant to subdivision (a) (6) of \xA7 55-79.54 was adhered to. A copy of the amendment shall be delivered to the unit owner or unit owners of the unit or units concerned. If executed by the declarant, such an amendment recorded prior to July 1, 1983, shall not be invalid because it was not prepared by an officer of the unit owners' association.","D. If the declarant does not prepare and record an amendment to the declaration to effect the assignment of common elements as limited common elements in accordance with rights reserved in the condominium instruments, but has reflected an intention to make such assignments in deeds conveying units, then the principal officer of the unit owners' association may prepare, execute and record such an amendment at any time after the declarant ceases to be a unit owner.","E. The declarant may unilaterally record an amendment to the declaration converting a limited common element appurtenant to a unit owned by the declarant into a common element as long as the declarant continues to own the unit."]},{title:"\xA7 55-79.58. Contents of plats and plans.",text:["A. There shall be recorded simultaneously with the declaration one or more plats of survey showing the location and dimensions of the submitted land, the location and dimensions of any convertible lands within the submitted land, the location and dimensions of any existing improvements, the intended location and dimensions of any contemplated improvements which are to be located on any portion of the submitted land other than within the boundaries of any convertible lands, and, to the extent feasible, the location and dimensions of all easements appurtenant to the submitted land or otherwise submitted to this chapter as a part of the common elements. If the submitted land is not contiguous, then the plats shall indicate the distances between the parcels constituting the submitted land. The plats shall label every convertible land as a convertible land, and if there is more than one such land the plats shall label each such land with one or more letters and/or numbers different from those designating any other convertible land and different also from the identifying number of any unit. The plats shall show the location and dimensions of any withdrawable lands, and shall label each such land as a withdrawable land. The plats shall show the location and dimensions of any additional lands and shall label each such land as an additional land. If, with respect to any portion or portions, but less than all, of the submitted land, the unit owners are to own only an estate for years, the plats shall show the location and dimensions of any such portions, and shall label each such portion as a leased land. If there is more than one withdrawable land, or more than one leased land, the plats shall label each such land with one or more letters and/or numbers different from those designating any convertible land or other withdrawable or leased land, and different also from the identifying number of any unit. The plats shall show all easements to which the submitted land or any portion thereof is subject, and shall show the location and dimensions of all such easements to the extent feasible. The plats shall also show all encroachments by or on any portion of the condominium. In the case of any improvements located or to be located on any portion of the submitted land other than within the boundaries of any convertible lands, the plats shall indicate which, if any, have not been begun by the use of the phrase \"(NOT YET BEGUN),\" and which, if any, have been begun but have not been substantially completed by the use of the phrase \"(NOT YET COMPLETED).\" In the case of any units the vertical boundaries of which lie wholly or partially outside of structures for which plans pursuant to subsection B are simultaneously recorded, the plats shall show the location and dimensions of such vertical boundaries to the extent that they are not shown on such plans, and the units or portions thereof thus depicted shall bear their identifying numbers. Each plat shall be certified in a recorded document as to its accuracy and compliance with the provisions of this subsection by a licensed land surveyor, and the said surveyor shall certify in such document or on the face of the plat that all units or portions thereof depicted thereon pursuant to the preceding sentence of this subsection have been substantially completed. The specification within this subsection of items that shall be shown on the plats shall not be construed to mean that the plats shall not also show all other items customarily shown or hereafter required for land title surveys.","B. There shall also be recorded, simultaneously with the declaration, plans of every structure which contains or constitutes all or part of any unit or units, and which is located on any portion of the submitted land other than within the boundaries of any convertible lands. The plans shall show the location and dimensions of the vertical boundaries of each unit to the extent that such boundaries lie within or coincide with the boundaries of such structures, and the units or portions thereof thus depicted shall bear their identifying numbers. In addition, each convertible space thus depicted shall be labeled a convertible space. The horizontal boundaries of each unit having horizontal boundaries shall be identified on the plans with reference to established datum. Unless the condominium instruments expressly provide otherwise, it shall be presumed that in the case of any unit not wholly contained within or constituting one or more such structures, the horizontal boundaries thus identified extend, in the case of each such unit, at the same elevation with regard to any part of such unit, lying outside of such structures, subject to the following exception: In the case of any such unit which does not lie over any other unit other than basement units, it shall be presumed that the lower horizontal boundary, if any, of that unit lies at the level of the ground with regard to any part of that unit lying outside of such structures. The plans shall be certified on their face or in another recorded document as to their accuracy and compliance with the provisions of this subsection by a licensed architect, licensed engineer or licensed land surveyor, and the said architect, engineer or land surveyor shall certify on the plans or in the recorded document that all units or portions thereof depicted thereon have been substantially completed.","C. When converting all or any portion of any convertible land, or adding additional land to an expandable condominium, the declarant shall record, with regard to any structures on the land being converted, or added, either plats of survey conforming to the requirements of subsection A and plans conforming to the requirements of subsection B, or certifications, conforming to the certification requirements of said subsections, of plats and plans previously recorded pursuant to \xA7 55-79.59.","D. Notwithstanding the provisions of subsection A and B, a time-share interest in a unit which has been subjected to a time-share instrument pursuant to \xA7 55-367 may be conveyed prior to substantial completion of that unit if (i) a completion bond has been filed in compliance with subsection B of \xA7 55-79.58:1 and remains in full force and effect until the unit is certified as substantially complete in accordance with subsections A and B and (ii) the settlement agent or title insurance company insuring the time-share estate in the unit certifies to the purchaser in writing, based on information provided by the Common Interest Community Board, that the bond has been filed with the Common Interest Community Board.","E. When converting all or any portion of any convertible space into one or more units and/or limited common elements, the declarant shall record, with regard to the structure or portion thereof constituting that convertible space, plans showing the location and dimensions of the vertical boundaries of each unit and/or limited common elements formed out of such space. Such plans shall be certified as to their accuracy and compliance with the provisions of this subsection by a licensed architect, licensed engineer or licensed land surveyor.","F. For the purposes of subsections A, B, and C, all provisions and requirements relating to units shall be deemed equally applicable to limited common elements. The limited common elements shall be labeled as such, and each limited common element depicted on the plats and plans shall bear the identifying number or numbers of the unit or units to which it is assigned, if it has been assigned, unless the provisions of subsection (e) of \xA7 55-79.50 make such designations unnecessary."]},{title:"\xA7 55-79.58:1. Bond to insure completion of improvements.",text:["A. The declarant shall file with the Common Interest Community Board a bond entered into by the declarant in the sum of 100 percent of the estimated cost of completion, to the extent of the declarant's obligation as stated in the declaration, of all improvements to the common elements of the condominium labeled in the plat or plats as \"(NOT YET COMPLETED)\" or \"(NOT YET BEGUN)\" located upon submitted land and which the declarant reasonably believes will not be substantially complete at the time of conveyance of the first condominium unit. Such bond shall be conditioned upon the faithful performance of the declarant's obligation to complete said improvements in strict conformity with the plans and specifications for the same as described in the declaration.","B. The declarant shall file with the Common Interest Community Board a bond entered into by the declarant in the sum of 100 percent of the estimated cost of completion of a unit in which a time-share interest is conveyed before the unit has been certified as substantially complete in accordance with subsections A and B of \xA7 55-79.58. The bond required by this subsection shall be conditioned upon the faithful performance of the declarant's obligation to complete said improvements in strict conformity with the plans and specifications for the same as described in the declaration.","C. All bonds required herein shall be executed by a surety company authorized to transact business in the Commonwealth of Virginia or by such other surety as is satisfactory to the Board.","D. The Board may promulgate reasonable regulations which govern the return of bonds submitted in accordance with this section."]},{title:"\xA7 55-79.59. Preliminary recordation of plats and plans.",text:["Plats and plans previously recorded pursuant to \xA7 55-79.54 (a), (b) and (c) may be used in lieu of new plats and plans to satisfy in whole or in part the requirements of \xA7 55-79.56 (b), \xA7 55-79.61 B and/or \xA7 55-79.63 if certifications thereof are recorded by the declarant in accordance with \xA7 55-79.58 A and B; and if such certifications are recorded, the plats and plans which they certify shall be deemed recorded pursuant to \xA7 55-79.58 C within the meaning of the three sections aforesaid. All condominium instruments for condominiums created prior to July 1, 1991, are hereby validated notwithstanding that the plats were prerecorded as if in compliance with this section and not recorded with amendments converting convertible land or adding additional land if the plats or subsequent amendments contained the required certifications."]},{title:"\xA7 55-79.60. Easement for encroachments.",text:["To the extent that any unit or common element encroaches on any other unit or common element, whether by reason of any deviation from the plats and plans in the construction, repair, renovation, restoration, or replacement of any improvement, or by reason of the settling or shifting of any land or improvement, a valid easement for such encroachment shall exist. The purpose of this section is to protect the unit owners, except in cases of willful and intentional misconduct by them or their agents or employees, and not to relieve the declarant or any contractor, subcontractor, or materialman of any liability which any of them may have by reason of any failure to adhere strictly to the plats and plans."]},{title:"\xA7 55-79.61. Conversion of convertible lands.",text:["A. The declarant may convert all or any portion of any convertible land into one or more units and/or limited common elements subject to any restrictions and limitations which the condominium instruments may specify. Any such conversion shall be deemed to have occurred at the time of the recordation of appropriate instruments pursuant to subsection B of this section and subsection C of \xA7 55-79.58.","B. Simultaneously with the recording of plats and plans pursuant to subsection C of \xA7 55-79.58, the declarant shall prepare, execute, and record an amendment to the declaration describing the conversion. Such amendment shall assign an identifying number to each unit formed out of a convertible land and shall reallocate undivided interests in the common elements in accordance with subsection (b) of \xA7 55-79.56. Such amendment shall describe or delineate the limited common elements formed out of the convertible land, showing or designating the unit or units to which each is assigned.","C. All convertible lands shall be deemed a part of the common elements except for such portions thereof as are converted in accordance with the provisions of this section. Until the expiration of the period during which conversion may occur or until actual conversion, whichever occurs first, the declarant alone shall be liable for real estate taxes assessed against the convertible land and any improvements thereon and all other expenses in connection with that real estate, and no other unit owner and no other portion of the condominium shall be subject to a claim for payment of those taxes or expenses, and unless the declaration provides otherwise, any income or proceeds from the convertible land and any improvements thereon shall inure to the declarant. No such conversion shall occur after 10 years from the recordation of the declaration, or such shorter period of time as the declaration may specify."]},{title:"\xA7 55-79.62. Conversion of convertible spaces.",text:["(a) The declarant may convert all or any portion of any convertible space into one or more units and/or common elements, including, without limitation, limited common elements, subject to any restrictions and limitations which the condominium instruments may specify. Any such conversion shall be deemed to have occurred at the time of the recordation of appropriate instruments pursuant to subsection (b) hereof and subsection B of \xA7 55-79.58.","(b) Simultaneously with the recording of plats and plans pursuant to subsection E of \xA7 55-79.58, the declarant shall prepare, execute, and record an amendment to the declaration describing the conversion. Such amendment shall assign an identifying number to each unit formed out of a convertible space and shall allocate to each unit a portion of the undivided interest in the common elements appertaining to that space. Such amendment shall describe or delineate the limited common elements formed out of the convertible space, showing or designating the unit or units to which each is assigned.","(c) If all or any portion of any convertible space is converted into one or more units in accordance with this section, the declarant shall prepare and execute, and record simultaneously with the amendment to the declaration, an amendment to the bylaws. The amendment to the bylaws shall reallocate votes in the unit owners' association, rights to future common profits, and liabilities for future common expenses not specially assessed, all as in the case of the subdivision of a unit in accordance with subsection D of \xA7 55-79.70.","(d) Any convertible space not converted in accordance with the provisions of this section, or any portion or portions thereof not so converted, shall be treated for all purposes as a single unit until and unless it is so converted, and the provisions of this chapter shall be deemed applicable to any such space, or portion or portions thereof, as though the same were a unit."]},{title:"\xA7 55-79.63. Expansion of condominium.",text:["No condominium shall be expanded except in accordance with the provisions of the declaration and of this chapter. Any such expansion shall be deemed to have occurred at the time of the recordation of plats and plans pursuant to subsection C of \xA7 55-79.58, together with an amendment to the declaration, duly executed by the declarant, including, without limitation, all of the owners and lessees of the additional land added to the condominium. Such amendment shall contain a legal description by metes and bounds of the land added to the condominium, and shall reallocate undivided interests in the common elements in accordance with the provisions of subsection (b) of \xA7 55-79.56. Such amendment may create convertible or withdrawable lands or both within the land added to the condominium, but this provision shall not be construed in derogation of the time limits imposed by or pursuant to subdivision (d) (3) of \xA7 55-79.54 and subsection C of \xA7 55-79.61."]},{title:"\xA7 55-79.64. Contraction of condominium.",text:["No condominium shall be contracted except in accordance with the provisions of the declaration and of this chapter. Any such contraction shall be deemed to have occurred at the time of the recordation of an amendment to the declaration, executed by the declarant, containing a legal description by metes and bounds of the land withdrawn from the condominium. If portions of the withdrawable land were described pursuant to subdivision (d) (5) of \xA7 55-79.54, then no such portion shall be so withdrawn after the conveyance of any unit on such portion. If no such portions were described, then none of the withdrawable land shall be withdrawn after the first conveyance of any unit thereon."]},{title:"\xA7 55-79.65. Easement to facilitate conversion and expansion.",text:["Subject to any restrictions and limitations the condominium instruments may specify, the declarant shall have a transferable easement over and on the common elements for the purpose of making improvements on the submitted land and any additional land pursuant to the provisions of those instruments and of this chapter, and for the purpose of doing all things reasonably necessary and proper in connection therewith."]},{title:"\xA7 55-79.66. Easement to facilitate sales.",text:["The declarant and his duly authorized agents, representatives, and employees may maintain sales offices and/or model units on the submitted land if and only if the condominium instruments provide for the same and specify the rights of the declarant with regard to the number, size, location, and relocation thereof. Any such sales office or model unit which is not designated a unit by the condominium instruments shall become a common element as soon as the declarant ceases to be a unit owner, and the declarant shall cease to have any rights with regard thereto unless such sales office or model unit is removed forthwith from the submitted land in accordance with a right reserved in the condominium instruments to make such removal."]},{title:"\xA7 55-79.67. Declarant's obligation to complete and restore.",text:["(a) No covenants, restrictions, limitations, or other representations or commitments in the condominium instruments with regard to anything that is or is not to be done on the additional land, the withdrawable land, or any portion of either, shall be binding as to any portion of either lawfully withdrawn from the condominium or never added thereto except to the extent that the condominium instruments so provide. But in the case of any covenant, restriction, limitation, or other representation or commitment in the condominium instruments or in any other agreement requiring the declarant to add all or any portion of the additional land or to withdraw any portion of the withdrawable land, or imposing any obligations with regard to anything that is or is not to be done thereon or with regard thereto, or imposing any obligations with regard to anything that is or is not to be done on or with regard to the condominium or any portion thereof, this subsection shall not be construed to nullify, limit, or otherwise affect any such obligation.","(a1) The declarant shall complete all improvements labeled \"(NOT YET COMPLETED)\" on plats recorded pursuant to the requirements of this chapter unless the condominium instruments expressly exempt the declarant from such obligation, and shall, in the case of every improvement labeled \"(NOT YET BEGUN)\" on such plats, state in the declaration either the extent of the obligation to complete the same or that there is no such obligation.","(b) To the extent that damage is inflicted on any part of the condominium by any person or persons utilizing the easements reserved by the condominium instruments or created by \xA7\xA7 55-79.65 and 55-79.66, the declarant together with the person or persons causing the same shall be jointly and severally liable for the prompt repair thereof and for the restoration of the same to a condition compatible with the remainder of the condominium."]},{title:"\xA7 55-79.68. Alterations within units.",text:["(a) Except to the extent prohibited by the condominium instruments, and subject to any restrictions and limitations specified therein, any unit owner may make any improvements or alterations within his unit that do not impair the structural integrity of any structure or otherwise lessen the support of any portion of the condominium. But no unit owner shall do anything which would change the exterior appearance of his unit or of any other portion of the condominium except to such extent and subject to such conditions as the condominium instruments may specify.","(b) If a unit owner acquires an adjoining unit, or an adjoining part of an adjoining unit, then such unit owner shall have the right to remove all or any part of any intervening partition or to create doorways or other apertures therein, notwithstanding the fact that such partition may in whole or in part be a common element, so long as no portion of any bearing wall or bearing column is weakened or removed and no portion of any common element other than that partition is damaged, destroyed, or endangered. Such creation of doorways or other apertures shall not be deemed an alteration of boundaries within the meaning of \xA7 55-79.69."]},{title:"\xA7 55-79.69. Relocation of boundaries between units.",text:["A. If the condominium instruments expressly permit the relocation of boundaries between adjoining units, then the boundaries between such units may be relocated in accordance with (i) the provisions of this section and (ii) any restrictions and limitations not otherwise unlawful which the condominium instruments may specify. The boundaries between adjoining units shall not be relocated unless the condominium instruments expressly permit it.","B. If the unit owners of adjoining units whose mutual boundaries may be relocated desire to relocate such boundaries, then the principal officer of the unit owners' association, or such other officer or officers as the condominium instruments may specify, shall, upon written application of such unit owners, forthwith prepare and execute appropriate instruments pursuant to subsections C, D, and E.","C. An amendment to the declaration shall identify the units involved and shall state that the boundaries between those units are being relocated by agreement of the unit owners thereof, which amendment shall contain conveyancing between those unit owners. If the unit owners of the units involved have specified in their written application a reasonable reallocation as between the units involved of the aggregate undivided interest in the common elements appertaining to those units, the amendment to the declaration shall reflect that reallocation.","D. If the unit owners of the units involved have specified in their written application a reasonable reallocation as between the units involved of the aggregate number of votes in the unit owners' association allocated to those units, an amendment to the bylaws shall reflect that reallocation and a proportionate reallocation of liability for common expenses as between those units.","E. Such plats and plans as may be necessary to show the altered boundaries between the units involved together with their other boundaries shall be prepared, and the units depicted thereon shall bear their identifying numbers. Such plats and plans shall indicate the new dimensions of the units involved, and any change in the horizontal boundaries of either as a result of the relocation of their boundaries shall be identified with reference to established datum. Such plats and plans shall be certified as to their accuracy and compliance with the provisions of this subsection (i) by a licensed land surveyor in the case of any plat and (ii) by a licensed architect, licensed engineer or licensed land surveyor in the case of any plan.","F. When appropriate instruments in accordance with the preceding subsections hereof have been prepared, executed, and acknowledged, they shall be recorded by an officer of the unit owners' association following payment by the unit owners of the units involved of all reasonable costs for the preparation, acknowledgment and recordation thereof. Said instruments shall become effective when executed by the unit owners of the units involved and recorded, and the recordation thereof shall be conclusive evidence that the relocation of boundaries thus effectuated did not violate any restrictions or limitations specified by the condominium instruments and that any reallocations made pursuant to subsections C and D were reasonable.","G. Any relocation of boundaries between adjoining units shall be governed by this section and not by \xA7 55-79.70. Section 55-79.70 shall apply only to such subdivisions of units as are intended to result in the creation of two or more new units in place of the subdivided unit."]},{title:"\xA7 55-79.70. Subdivision of units.",text:["A. If the condominium instruments expressly permit the subdivision of any units, then such units may be subdivided in accordance with (i) the provisions of this section and (ii) any restrictions and limitations not otherwise unlawful which the condominium instruments may specify. No unit shall be subdivided unless the condominium instruments expressly permit it.","B. If the unit owner of any unit which may be subdivided desires to subdivide such unit, then the principal officer of the unit owners' association, or such other officer or officers as the condominium instruments may specify, shall, upon written application of the subdivider, as such unit owner shall henceforth be referred to in this section, forthwith prepare and execute appropriate instruments pursuant to subsections C, D and E.","C. An amendment to the declaration shall assign new identifying numbers to the new units created by the subdivision of a unit and shall allocate to those units, on a reasonable basis acceptable to the subdivider, all of the undivided interest in the common elements appertaining to the subdivided unit. The new units shall jointly share all rights, and shall be equally liable jointly and severally for all obligations, with regard to any limited common elements assigned to the subdivided unit except to the extent that the subdivider may have specified in his written application that all or any portions of any limited common element assigned to the subdivided unit exclusively should be assigned to one or more, but less than all of the new units, in which case the amendment to the declaration shall reflect the desires of the subdivider as expressed in such written application.","D. An amendment to the bylaws shall allocate to the new units, on a reasonable basis acceptable to the subdivider, the votes in the unit owners' association allocated to the subdivided unit, and shall reflect a proportionate allocation to the new units of the liability for common expenses formerly appertaining to the subdivided unit.","E. Such plats and plans as may be necessary to show the boundaries separating the new units together with their other boundaries shall be prepared, and the new units depicted thereon shall bear their new identifying numbers. Such plats and plans shall indicate the dimensions of the new units, and the horizontal boundaries thereof, if any, shall be identified thereon with reference to established datum. Such plats and plans shall be certified as to their accuracy and compliance with the provisions of this subsection (i) by a licensed land surveyor in the case of any plat and (ii) by a licensed architect, licensed engineer or licensed land surveyor in the case of any plan.","F. When appropriate instruments in accordance with the preceding subsections hereof have been prepared, executed, and acknowledged, they shall be recorded by an officer of the unit owners' association following payment by the subdivider of all reasonable costs for the preparation, acknowledgment and recordation thereof. Said instruments shall become effective when executed by the subdivider and recorded, and the recordation thereof shall be conclusive evidence that the subdivision thus effectuated did not violate any restrictions or limitations specified by the condominium instruments and that any reallocations made pursuant to subsections C and D were reasonable.","G. Notwithstanding the definition of \"unit\" found in \xA7 55-79.41 and the provisions of subsection (d) of \xA7 55-79.62, this section shall have no application to convertible spaces, and no such space shall be deemed a unit for the purposes of this section. However, this section shall apply to any units formed by the conversion of all or any portion of any such space, and any such unit shall be deemed a unit for the purposes of this section."]},{title:"\xA7 55-79.71. Amendment of condominium instruments.",text:["A. If there is no unit owner other than the declarant, the declarant may unilaterally amend the condominium instruments, and the amendment shall become effective upon the recordation thereof if the amendment has been executed by the declarant. But this section shall not be construed to nullify, limit, or otherwise affect the validity of enforceability of any agreement renouncing or to renounce, in whole or in part, the right hereby conferred.","B. If any of the units in the condominium is restricted exclusively to residential use and there is any unit owner other than the declarant, the condominium instruments shall be amended only by agreement of unit owners of units to which two-thirds of the votes in the unit owners' association appertain, or such larger majority as the condominium instruments may specify, except in cases for which this chapter provides different methods of amendment. If none of the units in the condominium is restricted exclusively to residential use, the condominium instruments may specify a majority smaller than the minimum specified in the preceding sentence.","C. An action to challenge the validity of an amendment adopted by the unit owners' association pursuant to this section may not be brought more than one year after the amendment is recorded.","D. Agreement of the required majority of unit owners to any amendment of the condominium instruments shall be evidenced by their execution of the amendment, or ratifications thereof, and the same shall become effective when a copy of the amendment is recorded together with a certification, signed by the principal officer of the unit owners' association or by such other officer or officers as the condominium instruments may specify, that the requisite majority of the unit owners signed the amendment or ratifications thereof.","E. Except to the extent expressly permitted or expressly required by other provisions of this chapter, or agreed to by 100 percent of the unit owners, no amendment to the condominium instruments shall change (i) the boundaries of any unit, (ii) the undivided interest in the common elements, (iii) the liability for common expenses, or (iv) the number of votes in the unit owners' association that appertains to any unit.","F. Notwithstanding any other provision of this section, the declarant may unilaterally execute and record a corrective amendment or supplement to the condominium instruments to correct a mathematical mistake, an inconsistency or a scrivener's error, or clarify an ambiguity in the condominium instruments with respect to an objectively verifiable fact (including without limitation recalculating the undivided interest in the common elements, the liability for common expenses or the number of votes in the unit owners' association appertaining to a unit), within five years after the recordation of the condominium instrument containing or creating such mistake, inconsistency, error or ambiguity. No such amendment or supplement may materially reduce what the obligations of the declarant would have been if the mistake, inconsistency, error or ambiguity had not occurred. Regardless of the date of recordation of the condominium instruments, the principal officer of the unit owners' association may also unilaterally execute and record such a corrective amendment or supplement upon a vote of two-thirds of the members of the executive organ. All corrective amendments and supplements recorded prior to July 1, 1986, are hereby validated to the extent that such corrective amendments and supplements would have been permitted by this subsection."]},{title:"\xA7 55-79.71:1. Use of technology.",text:["A. Unless the condominium instruments expressly provide otherwise, (i) any notice required to be sent or received or (ii) any signature, vote, consent, or approval required to be obtained under any condominium instrument or any provision of this chapter may be accomplished using the most advanced technology available at that time if such use is a generally accepted business practice. This section shall govern the use of technology in implementing the provisions of any condominium instrument or any provision of this chapter dealing with notices, signatures, votes, consents, or approvals.","B. Electronic transmission and other equivalent methods. The unit owners' association, unit owners, and other persons entitled to occupy a unit may perform any obligation or exercise any right under any condominium instrument or any provision of this chapter by use of any technological means providing sufficient security, reliability, identification, and verifiability. \"Acceptable technological means\" shall include without limitation electronic transmission over the Internet or the community or other network, whether by direct connection, intranet, telecopier, or electronic mail.","C. Signature requirements. An electronic signature meeting the requirements of applicable law shall satisfy any requirement for a signature under any condominium instrument or any provision of this chapter.","D. Voting rights. Voting, consent to and approval of any matter under any condominium instrument or any provision of this chapter may be accomplished by electronic transmission or other equivalent technological means provided that a record is created as evidence thereof and maintained as long as such record would be required to be maintained in nonelectronic form.","E. Acknowledgment not required. Subject to other provisions of law, no action required or permitted by any condominium instrument or any provision of this chapter need be acknowledged before a notary public if the identity and signature of such person can otherwise be authenticated to the satisfaction of the executive organ.","F. Nontechnology alternatives. If any person does not have the capability or desire to conduct business using electronic transmission or other equivalent technological means, the unit owners' association shall make reasonable accommodation, at its expense, for such person to conduct business with the unit owners' association without use of such electronic or other means.","G. This section shall not apply to any notice related to an enforcement action by the unit owners' association, an assessment lien, or foreclosure proceedings in enforcement of an assessment lien."]},{title:"\xA7 55-79.71:2. Merger or consolidation of condominiums; procedure.",text:["A. Any two or more condominiums, by agreement of the unit owners as provided in subsection B, may be merged or consolidated into a single condominium. In the event of a merger or consolidation, unless the agreement otherwise provides, the resultant condominium shall be the legal successor, for all purposes, of all of the preexisting condominiums, and the operations and activities of all unit owners' associations of the preexisting condominiums shall be merged or consolidated into a single unit owners' association that holds all powers, rights, obligations, assets, and liabilities of all preexisting unit owners' associations.","B. An agreement to merge or consolidate two or more condominiums pursuant to subsection A shall be evidenced by an agreement prepared, executed, recorded, and certified by the principal officer of the unit owners' association of each of the preexisting condominiums following approval by owners of units to which are allocated the percentage of votes in each condominium required to terminate that condominium. The agreement shall be recorded in every locality in which a portion of the condominium is located and shall not be effective until recorded.","C. Every merger or consolidation agreement shall provide for the reallocation of the allocated interests in the new unit owners' association among the units of the resultant condominium either (i) by stating the reallocations or the formulas upon which they are based or (ii) by stating the percentage of the overall allocated interests of the condominium that are allocated to all of the units comprising each of the preexisting condominiums, and provided that the portion of the percentages allocated to each unit formerly comprising a part of the preexisting condominium shall be equal to the percentages of allocated interests allocated to that unit by the declaration of the preexisting condominium.","D. If the condominium instruments of a condominium to be merged or consolidated require a vote or consent of mortgagees in order to amend the condominium instruments or terminate the condominium, the same vote or consent of mortgagees shall be required before such merger or consolidation shall become effective. No merger or consolidation shall affect mortgagee rights, alter the priority of the lien of any mortgage, materially impair or affect any condominium unit as collateral for a mortgage, or affect a mortgagee's right to foreclose on a condominium unit as collateral without the prior written consent of the mortgagee. A vote or consent of a mortgagee required by this section may be deemed received pursuant to \xA7 55-79.73:1."]},{title:"\xA7 55-79.72. Repealed.",text:["Repealed by Acts 1993, c. 667."]},{title:"\xA7 55-79.72:1. Termination of condominium.",text:["A. If there is no unit owner other than the declarant, the declarant may unilaterally terminate the condominium. An instrument terminating a condominium shall become effective upon recordation thereof if the termination instrument has been signed by the declarant. But this section shall not be construed to nullify, limit, or otherwise affect the validity or enforceability of any agreement renouncing or to renounce, in whole or in part, the right hereby conferred.","B. Except in the case of a taking of all the units by eminent domain, if any of the units in the condominium is restricted exclusively to residential use and there is any unit owner other than the declarant, the condominium may be terminated only by the agreement of unit owners of units to which four-fifths of the votes in the unit owners' association appertain, or such larger majority as the condominium instruments may specify. If none of the units in the condominium is restricted exclusively to residential use, the condominium instruments may specify a majority smaller than the minimum specified in the preceding sentence.","C. Agreement of the required majority of unit owners to termination of the condominium shall be evidenced by their execution of a termination agreement, or ratifications thereof, and the same shall become effective when a copy of the termination agreement is recorded together with a certification, signed by the principal officer of the unit owners' association or by such other officer or officers as the condominium instruments may specify, that the requisite majority of the unit owners signed the termination agreement or ratifications thereof. Unless the termination agreement otherwise provides, prior to recordation of the termination agreement, a unit owner's prior agreement to terminate the condominium may be revoked only with the approval of unit owners of units to which a majority of the votes in the unit owners' association appertain. The termination agreement shall specify a date after which the termination agreement shall be void if the termination agreement is not recorded. For the purposes of this section, an instrument terminating a condominium and any ratification thereof shall be deemed a condominium instrument subject to the provisions of \xA7 55-79.49.","D. In the case of a condominium that contains only units having horizontal boundaries described in the condominium instruments, a termination agreement may provide that all the common elements and units of the condominium shall be sold following termination. If, pursuant to the termination agreement, any property in the condominium is sold following termination, the termination agreement shall set forth the minimum terms of the sale.","E. In the case of a condominium that contains any units not having horizontal boundaries described in the condominium instruments, a termination agreement may provide for sale of the common elements. The termination agreement may not require that the units be sold following termination, unless the condominium instruments as originally recorded provide otherwise or all the unit owners consent to the sale. In the case of a master condominium that contains a unit which is a part of another condominium, a termination agreement for the master condominium shall not terminate the other condominium.","F. On behalf of the unit owners, the unit owners' association may contract for the disposition of property in the condominium, but the contract shall not be binding on the unit owners until approved pursuant to subsections B and C of this section. If the termination agreement requires that any property in the condominium be sold following termination, title to the property, upon termination, shall vest in the unit owners' association as trustee for the holders of all interest in the units. Thereafter, the unit owners' association shall have powers necessary and appropriate to effect the sale. Until the same has been concluded and the proceeds have been distributed, the unit owners' association shall continue in existence with all the powers the unit owners' association had before termination. Proceeds of the sale shall be distributed to unit owners and lien holders as their interests may appear, in proportion to the respective interests of the unit owners as provided in subsection I of this section. Unless otherwise specified in the termination agreement, for as long as the unit owners' association holds title to the property, each unit owner or his successor in interest shall have an exclusive right to occupancy of the portion of the property that formerly constituted his unit. During the period of occupancy by the unit owner or his successor in interest, each unit owner or his successor in interest shall remain liable for any assessment or other obligation imposed on the unit owner by this chapter or the condominium instruments.","G. If the property that constitutes the condominium is not sold following termination, title to the common elements and, in the case of a condominium containing only units that have horizontal boundaries described in the condominium instruments, title to all the property in the condominium shall vest in the unit owners, upon termination, as tenants in common in proportion to the unit owners' respective interests as provided in subsection I of this section. Any liens on the units shall shift accordingly. While the tenancy in common exists, each unit owner or his successor in interest shall have the exclusive right to occupancy of the portion of the property that formerly constituted the unit owner's unit.","H. Following termination of the condominium, the proceeds of any sale of property, together with the assets of the unit owners' association, shall be held by the unit owners' association as trustee for unit owners or lien holders on the units as their interests may appear. Following termination, any creditor of the unit owners' association who holds a lien on the unit that was recorded before termination may enforce the lien in the same manner as any lien holder. Any other creditor of the unit owners' association shall be treated as if he had perfected a lien on the units immediately before termination.","I. Unless the condominium instruments as originally recorded or as amended by 100 percent of the unit owners provide otherwise, the respective interests of unit owners referred to in subsections F, G, and H shall be as follows:","1. Except as provided in subdivision 2, the respective interests of the unit owners shall be the fair market values of their units, limited common elements, and common element interests immediately before the termination, as determined by one or more independent appraisers selected by the unit owners' association. The decision of the independent appraisers shall be distributed to the unit owners and become final unless disapproved within thirty days after distribution by unit owners of units to which one-quarter of the votes in the unit owners' association appertain. The proportion of any unit owner's interest to the interest of all unit owners is determined by dividing the fair market value of that unit owner's unit and common element interest by the total fair market values of all the units and their common element interests.","2. If any unit or limited common element is destroyed to the extent that an appraisal of the fair market value thereof before destruction cannot be made, the interests of all unit owners are the unit owners' respective common element interests immediately before the termination.","J. Except as provided in subsection K, foreclosure or enforcement of a lien or encumbrance against the entire condominium shall not alone terminate the condominium, and foreclosure or enforcement of a lien or encumbrance against a portion of the condominium, other than withdrawable land, shall not withdraw that portion from the condominium. Foreclosure or enforcement of a lien or encumbrance against withdrawable land shall not alone withdraw the land from the condominium, but the person who takes title to the withdrawable land shall have the right to require from the unit owners' association, upon request, an amendment that excludes the land from the condominium.","K. If a lien or encumbrance against a portion of the property that comprises the condominium has priority over the condominium instruments and the lien or encumbrance has not been partially released, upon foreclosure, the parties foreclosing the lien or encumbrance may record an instrument that excludes the property subject to the lien or encumbrance from the condominium."]},{title:"\xA7 55-79.72:2. Rights of mortgagees.",text:["No provision of this chapter shall be construed in derogation of any requirement of the condominium instruments that all or a specified number of the beneficiaries of mortgages or deeds of trust encumbering the condominium units approve specified actions contemplated by the unit owners' association."]},{title:"\xA7 55-79.72:3. Statement of unit owner rights.",text:["Every unit owner who is a member in good standing of a unit owners' association shall have the following rights:","1. The right of access to all books and records kept by or on behalf of the unit owners' association according to and subject to the provisions of \xA7 55-79.74:1, including records of all financial transactions;","2. The right to cast a vote on any matter requiring a vote by the unit owners' association membership in proportion to the unit owner's ownership interest, except to the extent that the condominium instruments provide otherwise;","3. The right to have notice of any meeting of the executive organ, to make a record of such meetings by audio or visual means, and to participate in such meeting in accordance with the provisions of \xA7 55-79.75;","4. The right to have (i) notice of any proceeding conducted by the executive organ or other tribunal specified in the condominium instruments against the unit owner to enforce any rule or regulation of the unit owners' association and (ii) the opportunity to be heard and represented by counsel at the proceeding, as provided in \xA7 55-79.80:2, and the right of due process in the conduct of that hearing; and","5. The right to serve on the executive organ if duly elected and a member in good standing of the unit owners' association, except to the extent that the condominium instruments provide otherwise.","The rights enumerated in this section shall be enforceable by any such unit owner pursuant to the provisions of \xA7 55-79.53."]}];

