var vncaText2=[{title:"\xA7 13.1-815. Fees to be collected by Commission; payment of fees prerequisite to Commission action; exceptions.",text:["A. The Commission shall assess the registration fees and shall charge and collect the filing fees, charter fees and entrance fees imposed by law. The Commission shall have authority to certify to the Comptroller directing refund of any overpayment of a fee, or of any fee collected for a document that is not accepted for filing, at any time within one year from the date of its payment. When the Commission receives payment of an annual registration fee assessed against a domestic or foreign corporation, such payment shall be applied against any unpaid annual registration fees previously assessed against such corporation, including any penalties incurred thereon, beginning with the assessment or penalty that has remained unpaid for the longest period of time.","B. The Commission shall not file or issue with respect to any domestic or foreign corporation any document or certificate specified in this Act, except the annual report required by \xA7 13.1-936, a statement of change pursuant to \xA7 13.1-834 or 13.1-926, and a statement of resignation pursuant to \xA7 13.1-835 or 13.1-927, until all fees, charges, fines, penalties, and interest assessed, imposed, charged, or to be collected by the Commission pursuant to this Act or Title 12.1 have been paid by or on behalf of such corporation. Notwithstanding the foregoing, the Commission may file or issue any document or certificate with respect to a domestic or foreign corporation that has been assessed an annual registration fee if the document or certificate is filed or issued with an effective date that is on or before the due date of the corporation's annual registration payment in any year, provided that the Commission shall not issue a certificate of domestication with respect to a foreign corporation until the annual registration fee has been paid by or on behalf of that corporation.","C. A domestic or foreign corporation shall not be required to pay the annual registration fee assessed against it pursuant to subsection B of \xA7 13.1-936.1 in any year if (i) the Commission issues or files any of the following types of certificate or instrument and (ii) the certificate or instrument is effective on or before the annual registration fee due date:","1. A certificate of termination of corporate existence, a certificate of incorporation surrender, or a certificate of entity conversion for a domestic corporation;","2. A certificate of withdrawal for a foreign corporation;","3. A certificate of merger or an authenticated copy of an instrument of merger for a domestic or foreign corporation that has merged into a surviving domestic corporation or eligible entity, or into a surviving foreign corporation or eligible entity; or","4. An authenticated copy of an instrument of entity conversion for a foreign corporation that has converted to a different entity type.","The Commission shall cancel the annual registration fee assessments specified in this subsection that remain unpaid.","D. Annual registration fee assessments that have been paid shall not be refunded."]},{title:"\xA7 13.1-815.1. Charter and entrance fees for corporations.",text:["A. Every domestic corporation, upon the granting of its charter or upon domestication, shall pay a charter fee in the amount of $50 into the state treasury, and every foreign corporation shall pay an entrance fee of $50 into the state treasury for its certificate of authority to transact business in the Commonwealth.","B. For any foreign corporation that files articles of domestication and that had authority to transact business in the Commonwealth at the time of such filing, the charter fee to be charged upon domestication shall be an amount equal to the difference between the amount that would be required by this section and the amount already paid as an entrance fee by such corporation. For any foreign corporation that files an application for a certificate of authority to transact business in the Commonwealth and that had previously surrendered its articles of incorporation as a domestic corporation, the entrance fee to be charged upon obtaining a certificate of authority to transact business in the Commonwealth shall be an amount equal to the difference between the amount that would be required by this section and the amount already paid as a charter fee by such corporation."]},{title:"\xA7 13.1-816. Fees for filing documents or issuing certificates.",text:["The Commission shall charge and collect the following fees, except as provided in \xA7 12.1-21.2:","1. For filing any one of the following, the fee shall be $25:","a. Articles of incorporation, domestication, or incorporation surrender.","b. Articles of amendment or restatement.","c. Articles of merger.","d. Articles of correction.","e. An application of a foreign corporation for a certificate of authority to transact business in the Commonwealth.","f. An application of a foreign corporation for an amended certificate of authority to transact business in the Commonwealth.","g. A copy of an amendment to the articles of incorporation of a foreign corporation holding a certificate of authority to transact business in the Commonwealth.","h. A copy of articles of merger of a foreign corporation holding a certificate of authority to transact business in the Commonwealth.","i. A copy of an instrument of entity conversion of a foreign corporation holding a certificate of authority to transact business in the Commonwealth.","2. For filing any one of the following, the fee shall be $10:","a. An application to reserve or to renew the reservation of a corporate name.","b. A notice of transfer of a reserved corporate name.","c. An application for use of an indistinguishable name.","d. Articles of dissolution.","e. Articles of revocation of dissolution.","f. Articles of termination of corporate existence.","g. An application for withdrawal of a foreign corporation.","3. For issuing a certificate pursuant to \xA7 13.1-945, the fee shall be $6."]},{title:"\xA7 13.1-817. Repealed.",text:["Repealed by Acts 1991, c. 123."]}];

