var vncaText5=[{title:"\xA7 13.1-829. Corporate name.",text:["A. A corporate name shall not contain:","1. Any word or phrase that indicates or implies that it is organized for the purpose of conducting any business other than a business which it is authorized to conduct;","2. The word \"redevelopment\" unless the corporation is organized as an urban redevelopment corporation pursuant to Chapter 190 of the 1946 Acts of Assembly, as amended;","3. Any word, abbreviation, or combination of characters that states or implies the corporation is a limited liability company or a limited partnership; or","4. Any word or phrase that is prohibited by law for such corporation.","B. Except as authorized by subsection C, a corporate name shall be distinguishable upon the records of the Commission from:","1. The name of any corporation, whether issuing shares or not issuing shares, existing under the laws of the Commonwealth or authorized to transact business in the Commonwealth;","2. A corporate name reserved or registered under \xA7 13.1-631, 13.1-632, 13.1-830 or 13.1-831;","3. The designated name adopted by a foreign corporation, whether issuing shares or not issuing shares, because its real name is unavailable for use in the Commonwealth;","4. The name of a domestic limited liability company or a foreign limited liability company registered to transact business in the Commonwealth;","5. A limited liability company name reserved under \xA7 13.1-1013;","6. The designated name adopted by a foreign limited liability company because its real name is unavailable for use in the Commonwealth;","7. The name of a domestic business trust or a foreign business trust registered to transact business in the Commonwealth;","8. A business trust name reserved under \xA7 13.1-1215;","9. The designated name adopted by a foreign business trust because its real name is unavailable for use in the Commonwealth;","10. The name of a domestic limited partnership or a foreign limited partnership registered to transact business in the Commonwealth;","11. A limited partnership name reserved under \xA7 50-73.3; and","12. The designated name adopted by a foreign limited partnership because its real name is unavailable for use in the Commonwealth.","C. A domestic corporation may apply to the Commission for authorization to use a name that is not distinguishable upon the Commission's records from one or more of the names described in subsection B. The Commission shall authorize use of the name applied for if the other entity consents to the use in writing and submits an undertaking in form satisfactory to the Commission to change its name to a name that is distinguishable upon the records of the Commission from the name of the applying corporation.","D. The use of assumed names or fictitious names, as provided for in Chapter 5 (\xA7 59.1-69 et seq.) of Title 59.1, is not affected by this Act.","E. The Commission, in determining whether a corporate name is distinguishable upon its records from the name of any of the business entities listed in subsection B, shall not consider any word, phrase, abbreviation, or designation required or permitted under \xA7 13.1-544.1, subsection A of \xA7 13.1-630, subsection A of \xA7 13.1-1012, \xA7 13.1-1104, subsection A of \xA7 50-73.2, and subdivision A 2 of \xA7 50-73.78 to be contained in the name of a business entity formed or organized under the laws of the Commonwealth or authorized or registered to transact business in the Commonwealth."]},{title:"\xA7 13.1-830. Reserved name.",text:["A. A person may apply to the Commission to reserve the exclusive use of a corporate name, including a designated name for a foreign corporation. If the Commission finds that the corporate name applied for is distinguishable upon the records of the Commission, it shall reserve the name for the applicant's exclusive use for a 120-day period.","B. The owner of a reserved corporate name may renew the reservation for successive periods of 120 days each by filing with the Commission, during the 45-day period preceding the date of expiration of the reservation, a renewal application.","C. The owner of a reserved corporate name may transfer the reservation to another person by delivering to the Commission a notice of the transfer, signed by the applicant for whom the name was reserved, and specifying the name and address of the transferee.","D. A reserved corporate name may be used by its owner in connection with (i) the formation or an amendment to change the name of a domestic stock or nonstock corporation, limited liability company, business trust, or limited partnership; (ii) an application for a certificate of authority or registration to transact business in the Commonwealth as a foreign stock or nonstock corporation, limited liability company, business trust, or limited partnership; or (iii) an amended application for such authority or registration, provided that the proposed name complies with the provisions of \xA7 13.1-630, 13.1-762, 13.1-829, 13.1-924, 13.1-1012, 13.1-1054, 13.1-1214, 13.1-1244, 50-73.2, or 50-73.56, as the case may be."]},{title:"\xA7 13.1-831. Registered name.",text:["A. A foreign corporation may register its corporate name, or its corporate name with any addition required by \xA7 13.1-924, if the name is distinguishable upon the records of the Commission from the corporate names that are not available under subsection B of \xA7 13.1-829.","B. A foreign corporation registers its corporate name, or its corporate name with any addition required by \xA7 13.1-924, by:","1. Filing with the Commission (i) an application setting forth its corporate name, or its corporate name with any addition required by \xA7 13.1-924, the state or country and date of its incorporation, and a brief description of the nature of the business in which it is engaged; and (ii) a certificate setting forth that such corporation is in good standing, or a document of similar import, from the state or country of incorporation, executed by the official who has custody of the records pertaining to corporations; and","2. Paying to the Commission a registration fee in the amount of $20. Except as provided in subsection E, registration is effective for one year after the date an application is filed.","C. If the Commission finds that the corporate name applied for is available, it shall register the name for the applicant's exclusive use.","D. A foreign corporation whose registration is effective may renew it for the succeeding year by filing with the Commission, during the 60-day period preceding the date of expiration of the registration, a renewal application that complies with the requirements of subsection B, and paying a renewal fee of $20. The renewal application is effective when filed in accordance with this section and, except as provided in subsection E, renews the registration for one year after the date the registration would have expired if such subsequent renewal of the registration had not occurred.","E. A foreign corporation whose registration is effective may thereafter obtain a certificate of authority to transact business in the Commonwealth under the registered name or consent in writing to the use of that name by a corporation thereafter incorporated under this Act or by another foreign corporation thereafter authorized to transact business in the Commonwealth. The registration terminates when the domestic corporation is incorporated or the foreign corporation obtains a certificate of authority to transact business in the Commonwealth or consents to the authorization of another foreign corporation to transact business in the Commonwealth under the registered name.","F. A foreign corporation that has in effect a registration of its corporate name may release such name by filing a notice of release of a registered name with the Commission and by paying a fee of $10."]},{title:"\xA7 13.1-832. Repealed.",text:["Repealed by Acts 2007, c. 771, cl. 2."]}];

