var vncaText6=[{title:"\xA7 13.1-833. Registered office and registered agent.",text:["A. Each corporation shall continuously maintain in the Commonwealth:","1. A registered office that may be the same as any of its places of business; and","2. A registered agent, who shall be:","a. An individual who is a resident of the Commonwealth and either an officer or director of the corporation or a member of the Virginia State Bar, and whose business office is identical with the registered office; or","b. A domestic or foreign stock or nonstock corporation, limited liability company or registered limited liability partnership authorized to transact business in the Commonwealth, the business office of which is identical with the registered office; provided such a registered agent (i) shall not be its own registered agent and (ii) shall designate by instrument in writing, acknowledged before a notary public, one or more natural persons at the office of the registered agent upon whom any process, notice or demand may be served and shall continuously maintain at least one such person at that office. Whenever any such person accepts service, a photographic copy of such instrument shall be attached to the return.","B. The sole duty of the registered agent is to forward to the corporation at its last known address any process, notice or demand that is served on the registered agent."]},{title:"\xA7 13.1-834. Change of registered office or registered agent.",text:["A. A corporation may change its registered office or registered agent, or both, upon filing with the Commission a statement of change on a form prescribed and furnished by the Commission that sets forth:","1. The name of the corporation;","2. The address of its current registered office;","3. If the current registered office is to be changed, the post-office address, including the street and number, if any, of the new registered office, and the name of the city or county in which it is to be located;","4. The name of its current registered agent;","5. If the current registered agent is to be changed, the name of the new registered agent; and","6. That after the change or changes are made, the corporation will be in compliance with the requirements of \xA7 13.1-833.","B. A statement of change shall forthwith be filed with the Commission by a corporation whenever its registered agent dies, resigns or ceases to satisfy the requirements of \xA7 13.1-833.","C. A corporation's registered agent may sign a statement as required above if (i) the business address of the registered agent changes to another post office address within the Commonwealth or (ii) the name of the registered agent has been legally changed. A corporation's new registered agent may sign and submit for filing a statement as required above if (a) the former registered agent is a business entity that has been merged into the new registered agent, (b) the instrument of merger is on record in the office of the clerk of the Commission, and (c) the new registered agent is an entity that is qualified to serve as a registered agent pursuant to \xA7 13.1-833. In either instance, the registered agent or surviving entity shall forthwith file a statement as required above, which shall recite that a copy of the statement shall be mailed to the principal office address of the corporation on or before the business day following the day on which the statement is filed."]},{title:"\xA7 13.1-835. Resignation of registered agent.",text:["A. A registered agent may resign the agency appointment by signing and filing with the Commission a statement of resignation accompanied by a certification that the registered agent shall mail a copy thereof to the principal office of the corporation by certified mail on or before the business day following the day on which the statement is filed. The statement of resignation may include a statement that the registered office is also discontinued.","B. The agency appointment is terminated, and the registered office discontinued if so provided, on the thirty-first day after the date on which the statement was filed."]},{title:"\xA7 13.1-836. Service on corporation.",text:["A. A corporation's registered agent is the corporation's agent for service of process, notice, or demand required or permitted by law to be served on the corporation. The registered agent may by instrument in writing, acknowledged before a notary public, designate a natural person or persons in the office of the registered agent upon whom any such process, notice or demand may be served and may, by instrument in writing, authorize service of process by facsimile by the sheriff, provided acknowledgement of receipt of service is returned by facsimile to the sheriff. Whenever any person so designated by the registered agent accepts service of process or whenever service is by facsimile, a photographic copy of the instruments designating the person or authorizing the method of service and receipt shall be attached to the return.","B. Whenever a corporation fails to appoint or maintain a registered agent in the Commonwealth, or whenever its registered agent cannot with reasonable diligence be found at the registered office, then the clerk of the Commission shall be an agent of the corporation upon whom service may be made in accordance with \xA7 12.1-19.1.","C. This section does not prescribe the only means, or necessarily the required means, of serving a corporation."]}];

