var vncaText10=[{title:"\xA7 13.1-884. Authority to amend articles of incorporation.",text:["A. A corporation may amend its articles of incorporation at any time to add or change a provision that is required or permitted in the articles or to delete a provision not required in the articles. Whether a provision is required or permitted in the articles of incorporation is determined as of the effective date of the amendment.","B. A member of the corporation does not have a vested property right resulting from any provision in the articles of incorporation, including provisions relating to management, control, capital structure, purpose, or duration of the corporation."]},{title:"\xA7 13.1-885. Amendment of articles of incorporation by directors.",text:["A. Where there are no members, or no members having voting rights, an amendment shall be adopted at a meeting of the board of directors upon receiving the vote of at least two-thirds of the directors in office. The board may adopt one or more amendments at any one meeting.","B. Unless the articles of incorporation provide otherwise, a corporation's board of directors may adopt one or more amendments to the corporation's articles of incorporation without member action:","1. To delete the names and addresses of the initial directors;","2. To delete the name of the initial registered agent or the address of the initial registered office, if a statement of change described in \xA7 13.1-834 is on file with the Commission;","3. To add, delete, or change a geographic attribution for the name; or","4. To make any other change expressly permitted by this Act to be made without member action."]},{title:"\xA7 13.1-886. Amendment of articles of incorporation by directors and members.",text:["A. Where there are members having voting rights, except where member approval of an amendment of the articles of incorporation is not required by this Act, an amendment to the articles of incorporation shall be adopted in the following manner:","1. The proposed amendment shall be adopted by the board of directors;","2. After adopting the proposed amendment, the board of directors shall submit the amendment to the members for their approval. The board of directors shall also transmit to the members a recommendation that the members approve the amendment, unless the board of directors makes a determination that because of conflicts of interest or other special circumstances it should not make such a recommendation, in which case the board of directors shall transmit to the members the basis for that determination; and","3. The members entitled to vote on the amendment shall approve the amendment as provided in subsection D.","B. The board of directors may condition its submission of the proposed amendment on any basis.","C. The corporation shall notify each member entitled to vote of the proposed members' meeting in accordance with \xA7 13.1-842. The notice of meeting shall also state that the purpose, or one of the purposes, of the meeting is to consider the proposed amendment and contain or be accompanied by a copy of the amendment.","D. Unless this Act or the board of directors, acting pursuant to subsection B, requires a greater vote, the amendment to be adopted shall be approved by each voting group entitled to vote on the amendment by more than two-thirds of all the votes cast by that voting group. The articles of incorporation may provide for a greater or lesser vote than that provided for in this subsection or a vote by separate voting groups so long as the vote provided for is not less than a majority of all the votes cast on the amendment by each voting group entitled to vote on the amendment at a meeting at which a quorum of the voting group exists."]},{title:"\xA7 13.1-887. Voting on amendments by voting groups.",text:["The articles of incorporation may provide that members of a class are entitled to vote as a separate voting group on specified amendments of the articles of incorporation."]},{title:"\xA7 13.1-887.1. Amendment prior to organization.",text:["When a corporation has not yet completed its organization, its board of directors or incorporators, in the event that there is no board of directors, may adopt one or more amendments to the corporation's articles of incorporation."]},{title:"\xA7 13.1-888. Articles of amendment.",text:["A. A corporation amending its articles of incorporation shall file with the Commission articles of amendment setting forth:","1. The name of the corporation;","2. The text of each amendment adopted or the information required by subdivision L 5 of \xA7 13.1-804;","3. The date of each amendment's adoption;","4. If an amendment was adopted by the incorporators or the board of directors without member approval, a statement that the amendment was duly approved by the vote of at least two-thirds of the directors in office or by a majority of the incorporators, as the case may be, including the reason member and, if applicable, director approval was not required;","5. If an amendment was approved by the members, either:","a. A statement that the amendment was adopted by unanimous consent of the members; or","b. A statement that the amendment was proposed by the board of directors and submitted to the members in accordance with this Act and a statement of:","(1) The existence of a quorum of each voting group entitled to vote separately on the amendment; and","(2) Either the total number of votes cast for and against the amendment by each voting group entitled to vote separately on the amendment or the total number of undisputed votes cast for the amendment by each voting group and a statement that the number cast for the amendment by each voting group was sufficient for approval by that voting group.","B. If the Commission finds that the articles of amendment comply with the requirements of law and that all required fees have been paid, it shall issue a certificate of amendment."]},{title:"\xA7 13.1-889. Restated articles of incorporation.",text:["A. A corporation's board of directors may restate its articles of incorporation at any time with or without member approval.","B. The restatement may include one or more new amendments to the articles. If the restatement includes a new amendment requiring member approval, it shall be adopted and approved as provided in \xA7 13.1-886. If the restatement includes an amendment that does not require member approval, it shall be adopted as provided in \xA7 13.1-885.","C. If the board of directors submits a restatement for member approval, the corporation shall notify each member entitled to vote of the proposed members' meeting in accordance with \xA7 13.1-842. The notice shall also state that the purpose, or one of the purposes, of the meeting is to consider the proposed restatement and contain or be accompanied by a copy of the restatement that identifies any new amendment it would make in the articles.","D. A corporation restating its articles of incorporation shall file with the Commission articles of restatement setting forth:","1. The name of the corporation immediately prior to restatement;","2. Whether the restatement contains a new amendment to the articles;","3. The text of the restated articles of incorporation or amended and restated articles of incorporation, as the case may be;","4. Information required by subdivision L 5 of \xA7 13.1-804;","5. The date of the restatement's adoption;","6. If the restatement does not contain a new amendment to the articles, that the board of directors adopted the restatement;","7. If the restatement contains a new amendment to the articles not requiring member approval, the information required by subdivision A 4 of \xA7 13.1-888; and","8. If the restatement contains a new amendment to the articles requiring member approval, the information required by subdivision A 5 of \xA7 13.1-888.","E. If the Commission finds that the articles of restatement comply with the requirements of law and that all required fees have been paid, it shall issue a certificate of restatement. When the certificate of restatement is effective the restated articles of incorporation or amended and restated articles of incorporation supersede the original articles of incorporation and all amendments to them.","F. The Commission may certify restated articles of incorporation or amended and restated articles of incorporation as the articles of incorporation currently in effect."]},{title:"\xA7 13.1-890. Repealed.",text:["Repealed by Acts 2007, c. 925, cl. 2."]},{title:"\xA7 13.1-891. Effect of amendment of articles of incorporation.",text:["An amendment to the articles of incorporation does not affect a cause of action existing in favor of or against the corporation, a proceeding to which the corporation is a party, or the existing rights of persons other than members of the corporation. An amendment changing a corporation's name does not abate a proceeding brought by or against the corporation in its former name."]},{title:"\xA7 13.1-892. Amendment of bylaws by board of directors or members.",text:["A corporation's board of directors may amend or repeal the corporation's bylaws except to the extent that:","1. The articles of incorporation or \xA7 13.1-893 reserves that power exclusively to the members; or","2. The members in repealing, adopting, or amending a bylaw expressly provide that the board of directors may not amend, repeal, or reinstate that bylaw."]},{title:"\xA7 13.1-893. Bylaw provisions increasing quorum or voting requirements for directors.",text:["A. A bylaw that increases a quorum or voting requirement for the board of directors may be amended or repealed:","1. If originally adopted by the members, only by the members, unless the bylaws otherwise provide; or","2. If adopted by the board of directors, either by the members or by the board of directors.","B. A bylaw adopted or amended by the members that increases a quorum or voting requirement for the board of directors may provide that it shall be amended or repealed only by a specified vote of either the members or the board of directors.","C. Action by the board of directors under subsection A to amend or repeal a bylaw that changes the quorum or voting requirement applicable to meetings of the board of directors shall be effective only if it meets the quorum requirement and is adopted by the vote required to take action under the quorum and voting requirement then in effect."]}];

