var vncaText111=[{title:"\xA7 13.1-898.2. Domestication.",text:["A. A foreign corporation may become a domestic corporation if the laws of the jurisdiction in which the foreign corporation is incorporated authorize it to domesticate in another jurisdiction. The laws of the Commonwealth shall govern the effect of domesticating in the Commonwealth pursuant to this article.","B. A domestic corporation not required by law to be a domestic corporation may become a foreign corporation if the jurisdiction in which the corporation intends to domesticate allows for the domestication. Regardless of whether the laws of the foreign jurisdiction require the adoption of a plan of domestication, the domestication shall be approved in the manner provided in this article. The laws of the jurisdiction in which the corporation domesticates shall govern the effect of domesticating in that jurisdiction.","C. The plan of domestication shall set forth:","1. A statement of the jurisdiction in which the corporation is to be domesticated;","2. The terms and conditions of the domestication; and","3. For a foreign corporation that is to become a domestic corporation, as a referenced attachment, amended and restated articles of incorporation that comply with the requirements of \xA7 13.1-819 as they will be in effect upon consummation of the domestication.","D. The plan of domestication may include any other provision relating to the domestication.","E. The plan of domestication may also include a provision that the board of directors may amend the plan at any time prior to issuance of the certificate of domestication or such other document required by the laws of the other jurisdiction to consummate the domestication. Where a plan of domestication is required to be submitted to the members for their approval, an amendment made subsequent to the submission of the plan to the members of the corporation shall not alter or change any of the terms or conditions of the plan if such alteration or change would adversely affect the members of any class of the corporation."]},{title:"\xA7 13.1-898.3. Action on plan of domestication by a domestic corporation.",text:["A. When the members of a domestic corporation have voting rights, a plan of domestication shall be adopted in the following manner:","1. The board of directors of the corporation shall adopt the plan of domestication.","2. After adopting a plan of domestication, the board of directors shall submit the plan of domestication for approval by the members.","3. For a plan of domestication to be approved:","a. The board of directors shall recommend the plan to the members unless the board of directors determines that because of conflict of interests or other special circumstances it should make no recommendation and communicates the basis for its determination to the members with the plan; and","b. The members shall approve the plan as provided in subdivision 6 of this subsection.","4. The board of directors may condition its submission of the plan of domestication to the members on any basis.","5. The corporation shall notify each member entitled to vote of the proposed members' meeting in accordance with \xA7 13.1-842 at which the plan of domestication is to be submitted for approval. The notice shall state that a purpose of the meeting is to consider the plan and shall contain or be accompanied by a copy of the plan.","6. Unless this Act or the board of directors, acting pursuant to subdivision 4 of this subsection, requires a greater vote, the plan of domestication shall be approved by each voting group entitled to vote on the plan by more than two-thirds of all the votes entitled to be cast by that voting group. The articles of incorporation may provide for a greater or lesser vote than that provided for in this subdivision or a vote by separate voting groups so long as the vote provided for is not less than a majority of all the votes cast on the plan by each voting group entitled to vote on the plan at a meeting at which a quorum of the voting group exists.","7. Voting by a class of members as a separate voting group is required on a plan of domestication if the plan contains a provision that, if contained in a proposed amendment to articles of incorporation, would entitle the class to vote as a separate voting group on the proposed amendment under \xA7 13.1-887.","B. When a domestic corporation has no members, or no members have voting rights, a plan of domestication shall be adopted at a meeting of the board of directors of such corporation upon receiving the vote of a majority of the directors in office."]},{title:"\xA7 13.1-898.4. Articles of domestication.",text:["A. After the domestication of a foreign corporation is approved in the manner required by the laws of the jurisdiction in which the corporation is incorporated, the corporation shall file with the Commission articles of domestication setting forth:","1. The name of the corporation immediately prior to the filing of the articles of domestication and, if that name is unavailable for use in the Commonwealth or the corporation desires to change its name in connection with the domestication, a name that satisfies the requirements of \xA7 13.1-829;","2. The plan of domestication;","3. The original jurisdiction of the corporation and the date the corporation was incorporated in that jurisdiction, and each subsequent jurisdiction and the date the corporation was domesticated in each such jurisdiction, if any, prior to the filing of the articles of domestication; and","4. A statement that the domestication is permitted by the laws of the jurisdiction in which the corporation is incorporated and that the corporation has complied with those laws in effecting the domestication.","B. If the Commission finds that the articles of domestication comply with the requirements of law and that all required fees have been paid, it shall issue a certificate of domestication.","C. The certificate of domestication shall become effective pursuant to \xA7 13.1-806.","D. A foreign corporation's existence as a domestic corporation shall begin when the certificate of domestication is effective. Upon becoming effective, the certificate of domestication shall be conclusive evidence that all conditions precedent required to be performed by the foreign corporation have been complied with and that the corporation has been incorporated under this Act.","E. If the foreign corporation is authorized to transact business in the Commonwealth under Article 14 (\xA7 13.1-919 et seq.), its certificate of authority shall be canceled automatically on the effective date of the certificate of domestication issued by the Commission."]},{title:"\xA7 13.1-898.5. Surrender of articles of incorporation upon domestication.",text:["A. Whenever a domestic corporation has adopted and approved, in the manner required by this article, a plan of domestication providing for the corporation to be domesticated under the laws of another jurisdiction, the corporation shall file with the Commission articles of incorporation surrender setting forth:","1. The name of the corporation;","2. The jurisdiction in which the corporation is to be domesticated and the name of the corporation upon its domestication under the laws of that jurisdiction;","3. The plan of domestication;","4. A statement that the articles of incorporation surrender are being filed in connection with the domestication of the corporation as a foreign corporation to be incorporated under the laws of another jurisdiction and that the corporation is surrendering its charter under the laws of the Commonwealth;","5. Where the members of the corporation have voting rights, a statement:","a. That the plan was adopted by the unanimous consent of the members; or","b. That the plan was submitted to the members by the board of directors in accordance with this Act, and a statement of:","(1) The existence of a quorum of each voting group entitled to vote separately on the plan; and","(2) Either the total number of votes cast for and against the plan by each voting group entitled to vote separately on the plan or the total number of undisputed votes cast for the plan separately by each voting group and a statement that the number cast for the plan by each voting group was sufficient for approval by that voting group;","6. Where the corporation has no members, or no members having voting rights, then a statement of that fact, the date of the meeting of the board of directors at which the plan was adopted and a statement of the fact that such plan received the vote of a majority of the directors in office;","7. A statement that the corporation revokes the authority of its registered agent to accept service on its behalf and appoints the clerk of the Commission as its agent for service of process in any proceeding based on a cause of action arising during the time it was incorporated in the Commonwealth;","8. A mailing address to which the clerk may mail a copy of any process served on the clerk under subdivision 7; and","9. A commitment by the corporation to notify the clerk of the Commission in the future of any change in the mailing address of the corporation.","B. If the Commission finds that the articles of incorporation surrender comply with the requirements of law and that all required fees have been paid, it shall issue a certificate of incorporation surrender.","C. The corporation shall automatically cease to be a domestic corporation when the certificate of incorporation surrender becomes effective.","D. If the former domestic corporation intends to continue to transact business in the Commonwealth, then, within 30 days after the effective date of the certificate of incorporation surrender, it shall deliver to the Commission an application for a certificate of authority to transact business in the Commonwealth pursuant to \xA7 13.1-921 together with a copy of its instrument of domestication and articles of incorporation and all amendments thereto, duly authenticated by the Secretary of State or other official having custody of corporate records in the state or country under whose laws it is incorporated or domesticated.","E. Service of process on the clerk of the Commission is service of process on a former domestic corporation that has surrendered its charter pursuant to this section. Service on the clerk shall be made in accordance with \xA7 12.1-19.1 and service on the former domestic corporation may be made in any other manner permitted by law."]},{title:"\xA7 13.1-898.6. Effect of domestication.",text:["A. When a foreign corporation's certificate of domestication in the Commonwealth becomes effective, with respect to that corporation:","1. The title to all real estate and other property remains in the corporation without reversion or impairment;","2. The liabilities remain the liabilities of the corporation;","3. A proceeding pending may be continued by or against the corporation as if the domestication did not occur;","4. The articles of incorporation attached to the articles of domestication constitute the articles of incorporation of the corporation; and","5. The corporation is deemed to:","a. Be incorporated under the laws of the Commonwealth for all purposes;","b. Be the same corporation as the corporation that existed under the laws of the jurisdiction or jurisdictions in which it was originally incorporated or formerly domiciled; and","c. Have been incorporated on the date it was originally incorporated or organized.","B. Any member or director of a foreign corporation that domesticates into the Commonwealth who, prior to the domestication, was liable for the liabilities or obligations of the corporation is not released from those liabilities or obligations by reason of the domestication."]},{title:"\xA7 13.1-898.7. Abandonment of domestication.",text:["A. Unless otherwise provided in a plan of domestication of a domestic corporation to become a foreign corporation, after the plan has been approved and adopted as required by this article, and at any time before the certificate of incorporation surrender has become effective, the domestication may be abandoned by the domestic corporation without action by its members in accordance with any procedures set forth in the plan of domestication or, if no such procedures are set forth in the plan of domestication, in the manner determined by the board of directors.","B. If a domestication is abandoned as provided under subsection A after articles of incorporation surrender have been filed with the Commission but before the certificate of incorporation surrender has become effective, written notice that the domestication has been abandoned in accordance with this section shall be filed with the Commission prior to the effective date of the certificate of incorporation surrender. The notice shall take effect upon filing and the domestication shall be deemed abandoned and shall not become effective.","C. If the domestication of a foreign corporation into the Commonwealth is abandoned in accordance with the laws of the jurisdiction in which the foreign corporation is incorporated after articles of domestication have been filed with the Commission but before the certificate of domestication has become effective, written notice that the domestication has been abandoned shall be filed with the Commission prior to the effective time and date of the certificate of domestication. The notice shall take effect upon filing and the domestication shall be deemed abandoned and shall not become effective."]}];

