"use strict";
/* harmony export (binding) */ __webpack_require__.d(__webpack_exports__, "a", function() { return vncaText14; });
var vncaText14=[{title:"\xA7 13.1-919. Authority to transact business required.",text:["A. A foreign corporation may not transact business in the Commonwealth until it obtains a certificate of authority from the Commission.","B. The following activities, among others, do not constitute transacting business within the meaning of subsection A:","1. Maintaining, defending, or settling any proceeding;","2. Holding meetings of the board of directors or members or carrying on other activities concerning internal corporate affairs;","3. Maintaining bank accounts;","4. Selling through independent contractors;","5. Soliciting or obtaining orders, whether by mail or through employees or agents or otherwise, if the orders require acceptance outside the Commonwealth before they become contracts;","6. Creating or acquiring indebtedness, deeds of trust, and security interests in real or personal property;","7. Securing or collecting debts or enforcing deeds of trust and security interests in property securing the debts;","8. Owning, without more, real or personal property;","9. Conducting an isolated transaction that is completed within 30 days and that is not one in the course of repeated transactions of a like nature;","10. For a period of less than 90 consecutive days, producing, directing, filming, crewing or acting in motion picture feature films, television series or commercials, or promotional films which are sent outside of the Commonwealth for processing, editing, marketing and distribution; or","11. Serving, without more, as a general partner of or as a partner in a partnership that is a general partner of a domestic or foreign limited partnership that does not otherwise transact business in the Commonwealth.","C. The list of activities in subsection B is not exhaustive."]},{title:"\xA7 13.1-920. Consequences of transacting business without authority.",text:["A. A foreign corporation transacting business in the Commonwealth without a certificate of authority may not maintain a proceeding in any court in the Commonwealth until it obtains a certificate of authority.","B. Notwithstanding subsections A and C, the failure of a foreign corporation to obtain a certificate of authority does not impair the validity of its corporate acts or prevent it from defending any proceeding in the Commonwealth.","C. The successor to a foreign corporation that transacted business in the Commonwealth without a certificate of authority and the assignee of a cause of action arising out of that business may not maintain a proceeding based on that cause of action in any court in the Commonwealth until the foreign corporation or its successor obtains a certificate of authority.","A court may stay a proceeding commenced by a foreign corporation, its successor, or assignee until it determines whether the foreign corporation or its successor requires a certificate of authority. If it so determines, the court shall further stay the proceeding until the foreign corporation or its successor obtains the certificate.","D. If a foreign corporation transacts business in the Commonwealth without a certificate of authority, each officer, director, and employee who does any of such business in the Commonwealth knowing that a certificate of authority is required shall be liable for a penalty of not less than $500 and not more than $5,000. Any such penalty may be imposed by the Commission or by any court in the Commonwealth before which an action against the corporation may lie, after the corporation and the individual have been given notice and an opportunity to be heard.","E. Suits, actions and proceedings may be begun against a foreign corporation that transacts business in the Commonwealth without a certificate of authority by serving process on any director, officer or agent of the corporation doing such business, or, if none can be found, on the clerk of the Commission or on the corporation in any other manner permitted by law. If any foreign corporation transacts business in the Commonwealth without a certificate of authority, it shall by transacting such business be deemed to have thereby appointed the clerk of the Commission its attorney for service of process. Service upon the clerk shall be made in accordance with \xA7 12.1-19.1."]},{title:"\xA7 13.1-921. Application for certificate of authority.",text:["A. A foreign corporation may apply to the Commission for a certificate of authority to transact business in the Commonwealth. The application shall be made on forms prescribed and furnished by the Commission. The application shall set forth:","1. The name of the corporation, and if the corporation is prevented by \xA7 13.1-924 from using its name in the Commonwealth, a designated name that satisfies the requirements of subsection B of \xA7 13.1-924;","2. The name of the state or other jurisdiction under whose laws it is incorporated, and if the corporation was previously authorized or registered to transact business in the Commonwealth as a foreign corporation, limited liability company, business trust, limited partnership, or registered limited liability partnership, with respect to every such prior authorization or registration, (i) the name of the entity; (ii) the entity type; (iii) the state or other jurisdiction of incorporation, organization, or formation; and (iv) the entity identification number issued to it by the Commission;","3. The date of incorporation and period of duration;","4. The street address of the foreign corporation's principal office;","5. The address of the proposed registered office of the foreign corporation in the Commonwealth, including both (i) the post office address with street and number, if any, and (ii) the name of the county or city in which it is located, and the name of its proposed registered agent in the Commonwealth at such address and that the registered agent is either (a) an individual who is a resident of Virginia and either an officer or director of the corporation or a member of the Virginia State Bar or (b) a domestic or foreign stock or nonstock corporation, limited liability company, or registered limited liability partnership authorized to transact business in the Commonwealth, the business office of which is identical with the registered office; and","6. The names and usual business addresses of the current directors and principal officers of the foreign corporation.","B. The foreign corporation shall deliver with the completed application a copy of its articles of incorporation and all amendments thereto, duly authenticated by the Secretary of State or other official having custody of corporate records in the state or other jurisdiction under whose laws it is incorporated.","C. If the Commission finds that the application complies with the requirements of law, and that all required fees have been paid, it shall issue a certificate of authority to transact business in the Commonwealth."]},{title:"\xA7 13.1-922. Amended certificate of authority.",text:["A. A foreign corporation authorized to transact business in the Commonwealth shall obtain an amended certificate of authority from the Commission:","1. If it changes its corporate name or the state or other jurisdiction of its incorporation; or","2. To abandon or change the designated name adopted by the corporation for use in the Commonwealth pursuant to subsection B of \xA7 13.1-924.","B. The requirements of \xA7 13.1-921 for obtaining an original certificate of authority apply to obtaining an amended certificate under this section.","C. Whenever the articles of incorporation of a foreign corporation that is authorized to transact business in the Commonwealth are amended, within 30 days after the amendment becomes effective, the foreign corporation shall file with the Commission a copy of such amendment duly authenticated by the Secretary of State or other official having custody of corporate records in the state or other jurisdiction under whose law it is incorporated."]},{title:"\xA7 13.1-923. Effect of certificate of authority.",text:["A. A certificate of authority authorizes the foreign corporation to which it is issued to transact business in the Commonwealth, subject, however, to the right of the Commonwealth to revoke the certificate as provided in this Act.","B. A foreign corporation holding a valid certificate of authority shall have no greater rights and privileges than a domestic corporation. The certificate of authority shall not be deemed to authorize it to exercise any of its corporate powers or purposes that a foreign corporation is forbidden by law to exercise in the Commonwealth."]},{title:"\xA7 13.1-924. Corporate name of foreign corporation.",text:["A. No certificate of authority shall be issued to a foreign corporation unless the corporate name of such foreign corporation satisfies the requirements of \xA7 13.1-829.","B. If the corporate name of a foreign corporation does not satisfy the requirements of \xA7 13.1-829, to obtain or maintain a certificate of authority to transact business in the Commonwealth, if its real name is unavailable, the foreign corporation may use a designated name that is available, and that satisfies the requirements of \xA7 13.1-829, if it informs the Commission of the designated name."]},{title:"\xA7 13.1-925. Registered office and registered agent of foreign corporation.",text:["A. Each foreign corporation authorized to transact business in the Commonwealth shall continuously maintain in the Commonwealth:","1. A registered office, which may be the same as any of its places of business.","2. A registered agent, who shall be:","a. An individual who is a resident of Virginia and either an officer or director of the corporation or a member of the Virginia State Bar, and whose business office is identical with the registered office; or","b. A domestic or foreign stock or nonstock corporation, limited liability company or registered limited liability partnership authorized to transact business in the Commonwealth, the business office of which is identical with the registered office; provided such a registered agent (i) shall not be its own registered agent and (ii) shall designate by instrument in writing, acknowledged before a notary public, one or more natural persons at the office of the registered agent upon whom any process, notice or demand may be served and shall continuously maintain at least one such person at that office. Whenever any such person accepts service, a photographic copy of such instrument shall be attached to the return.","B. The sole duty of the registered agent is to forward to the corporation at its last known address any process, notice or demand that is served on the registered agent."]},{title:"\xA7 13.1-926. Change of registered office or registered agent of a foreign corporation.",text:["A. A foreign corporation authorized to transact business in the Commonwealth may change its registered office or registered agent, or both, upon filing with the Commission a statement of change on a form prescribed and furnished by the Commission that sets forth:","1. The name of the foreign corporation;","2. The address of its current registered office;","3. If the current registered office is to be changed, the post office address, including street and number, if any, of the new registered office, and the name of the city or county in which it is to be located;","4. The name of its current registered agent;","5. If the current registered agent is to be changed, the name of the new registered agent; and","6. That after the change or changes are made, the corporation will be in compliance with the requirements of \xA7 13.1-925.","B. A statement of change shall forthwith be filed with the Commission by a foreign corporation whenever its registered agent dies, resigns or ceases to satisfy the requirements of \xA7 13.1-925.","C. A foreign corporation's registered agent may sign a statement as required above if (i) the business address of the registered agent changes to another post office address within the Commonwealth or (ii) the name of the registered agent has been legally changed. A foreign corporation's new registered agent may sign and submit for filing a statement as required above if (a) the former registered agent is a business entity that has been merged into the new registered agent, (b) the instrument of merger is on record in the office of the clerk of the Commission, and (c) the new registered agent is an entity that is qualified to serve as a registered agent pursuant to \xA7 13.1-925. In either instance, the registered agent or surviving entity shall forthwith file a statement as required above, which shall recite that a copy of the statement shall be mailed to the principal office address of the foreign corporation on or before the business day following the day on which the statement is filed."]},{title:"\xA7 13.1-927. Resignation of registered agent of foreign corporation.",text:["A. The registered agent of a foreign corporation may resign the agency appointment by signing and filing with the Commission a statement of resignation accompanied by a certification that the registered agent shall mail a copy thereof to the principal office of the corporation by certified mail on or before the business day following the day on which the statement is filed. The statement of resignation may include a statement that the registered office is also discontinued.","B. The agency appointment is terminated, and the registered office discontinued if so provided, on the thirty-first day after the date on which the statement was filed."]},{title:"\xA7 13.1-928. Service of process on foreign corporation.",text:["A. The registered agent of a foreign corporation authorized to transact business in the Commonwealth shall be an agent of such corporation upon whom any process, notice, order or demand required or permitted by law to be served upon the corporation may be served. The registered agent may by instrument in writing, acknowledged before a notary public, designate a natural person or persons in the office of the registered agent upon whom any such process, notice, order or demand may be served. Whenever any such person accepts service of process, a photographic copy of such instrument shall be attached to the return.","B. Whenever a foreign corporation authorized to transact business in the Commonwealth fails to appoint or maintain a registered agent in the Commonwealth, or whenever its registered agent cannot with reasonable diligence be found at the registered office, then the clerk of the Commission shall be an agent of the corporation upon whom service may be made in accordance with \xA7 12.1-19.1.","C. Nothing in this section shall limit or affect the right to serve any process, notice, order or demand, required or permitted by law to be served upon a corporation in any other manner now or hereafter permitted by law."]},{title:"\xA7 13.1-928.1. Merger of foreign corporation authorized to transact business in Commonwealth.",text:["A. Whenever a foreign corporation authorized to transact business in the Commonwealth is a party to a merger permitted by the laws of the state or other jurisdiction under whose laws it is incorporated, and such corporation is the surviving entity of the merger, it shall, within 30 days after such merger becomes effective, file with the Commission a copy of the instrument of merger duly authenticated by the Secretary of State or other official having custody of corporate records in the state or other jurisdiction under whose law it is incorporated; however, the filing shall not be required when a foreign corporation merges with a domestic corporation, the foreign corporation's articles of incorporation are not amended by said merger, and the articles of merger filed on behalf of the domestic corporation pursuant to \xA7 13.1-896 contain a statement that the merger is permitted under the laws of the state or other jurisdiction in which the foreign corporation is incorporated and that the foreign corporation has complied with that law in effecting the merger.","B. Whenever a foreign corporation authorized to transact business in the Commonwealth is a party to a merger permitted by the laws of the state or other jurisdiction under the laws of which it is incorporated, and such corporation is not the surviving entity of the merger or, whenever such a foreign corporation is a party to a consolidation so permitted, the surviving or resulting domestic or foreign corporation, limited liability company, business trust, partnership, or limited partnership shall, if not continuing to transact business in the Commonwealth, within 30 days after such merger or consolidation becomes effective, deliver to the Commission a copy of the instrument of merger or consolidation duly authenticated by the Secretary of State or other official having custody of corporate records in the state or other jurisdiction under whose law it was incorporated and comply in behalf of the predecessor corporation with the provisions of \xA7 13.1-929. If a surviving or resulting corporation or limited liability company, business trust, partnership, or limited partnership is to continue to transact business in the Commonwealth and has not received a certificate of authority to transact business in the Commonwealth, within such 30 days, deliver to the Commission an application for a certificate of authority to transact business in the Commonwealth, together with a duly authenticated copy of the instrument of merger or consolidation and also, in case of a merger, a copy of its articles of incorporation and all amendments thereto, duly authenticated by the Secretary of State or other official having custody of corporate records in the state or country under whose laws it is incorporated.","C. Upon the merger or consolidation of two or more foreign corporations any one of which owns property in the Commonwealth, all such property shall pass to the surviving or resulting corporation except as otherwise provided by the laws of the state by which it is governed, but only from the time when a duly authenticated copy of the instrument of merger or consolidation is filed with the Commission."]},{title:"\xA7 13.1-928.2. Entity conversion of foreign corporation authorized to transact business in Commonwealth.",text:["A. Whenever a foreign corporation that is authorized to transact business in the Commonwealth converts to another type of entity, the surviving or resulting entity shall, within 30 days after such entity conversion becomes effective, file with the Commission a copy of the instrument of entity conversion duly authenticated by the Secretary of State or other official having custody of corporate records in the state or other jurisdiction under whose laws such entity conversion was effected; and","1. If the surviving or resulting entity is not continuing to transact business in the Commonwealth or is not a foreign limited liability company, business trust, limited partnership, or registered limited liability partnership, then, within 30 days after such entity conversion, it shall comply on behalf of the predecessor corporation with the provisions of \xA7 13.1-929; or","2. If the surviving or resulting entity is a foreign limited liability company, business trust, limited partnership, or registered limited liability partnership and is to continue to transact business in the Commonwealth, then, within such 30 days, it shall deliver to the Commission an application for a certificate of registration to transact business in the Commonwealth or, in the case of a foreign registered limited liability partnership, a statement of registration.","B. Upon the entity conversion of a foreign corporation that is authorized to transact business in the Commonwealth, all property in the Commonwealth owned by the foreign corporation shall pass to the surviving or resulting entity except as otherwise provided by the laws of the state or other jurisdiction by which it is governed, but only from and after the time when a duly authenticated copy of the instrument of entity conversion is filed with the Commission."]},{title:"\xA7 13.1-929. Withdrawal of foreign corporation.",text:["A. A foreign corporation authorized to transact business in the Commonwealth may not withdraw from the Commonwealth until it obtains a certificate of withdrawal from the Commission.","B. A foreign corporation authorized to transact business in the Commonwealth may apply to the Commission for a certificate of withdrawal. The application shall be on a form prescribed and furnished by the Commission and shall set forth:","1. The name of the foreign corporation and the name of the state or other jurisdiction under whose laws it is incorporated;","2. If applicable, a statement that the foreign corporation was a party to a merger permitted by the laws of the state or other jurisdiction under whose law it was incorporated and that it was not the surviving entity of the merger, has consolidated with another entity, or has converted to another type of entity under the laws of the state or other jurisdiction under whose law it was incorporated;","3. That the foreign corporation is not transacting business in the Commonwealth and that it surrenders its authority to transact business in the Commonwealth;","4. That the foreign corporation revokes the authority of its registered agent to accept service on its behalf and appoints the clerk of the Commission as its agent for service of process in any proceeding based on a cause of action arising during the time it was authorized to transact business in the Commonwealth;","5. A mailing address to which the clerk of the Commission may mail a copy of any process served on him under subdivision 4; and","6. A commitment to notify the clerk of the Commission in the future of any change in the mailing address of the corporation.","C. The Commission shall not allow any foreign corporation to withdraw from the Commonwealth unless such corporation files with the Commission a statement certifying that the corporation has filed returns and has paid all state taxes to the time of the certificate or a statement that no such returns are required to be filed or taxes are required to be paid. In such case the corporation may file returns and pay taxes before they would otherwise be due. If the Commission finds that the application complies with the requirements of law and that all required fees have been paid, it shall issue a certificate of withdrawal.","D. Before any foreign corporation authorized to transact business in the Commonwealth terminates its corporate existence, it shall file with the Commission an application for withdrawal. Whether or not such application is filed, the termination of the corporate existence of such foreign corporation shall not take away or impair any remedy available against such corporation for any right or claim existing or any liability incurred prior to such termination. Any such action or proceeding against such foreign corporation may be defended by such corporation in its corporate name. The members, directors and officers shall have power to take such corporate or other action as shall be appropriate to protect such remedy, right or claim. The right of a foreign corporation that has terminated its corporate existence to institute and maintain in its corporate name actions, suits or proceedings in the courts of the Commonwealth shall be governed by the law of the state of its incorporation.","E. Service of process on the clerk of the Commission is service of process on a foreign corporation that has withdrawn pursuant to this section. Service upon the clerk shall be made in accordance with \xA7 12.1-19.1, and service upon the foreign corporation may be made in any other manner permitted by law."]},{title:"\xA7 13.1-930. Automatic revocation of certificate of authority.",text:["A. If any foreign corporation fails to file its annual report or pay its annual registration fee in a timely manner as required by this chapter, the Commission shall mail to each such corporation notice of the impending revocation of its certificate of authority to transact business in the Commonwealth. Whether or not such notice is mailed, if any foreign corporation fails to file its annual report or pay its annual registration fee on or before the last day of the fourth month immediately following its annual report or annual registration fee due date each year, such foreign corporation shall automatically cease to be authorized to transact business in the Commonwealth and its certificate of authority shall be automatically revoked as of that day.","B. Every foreign corporation authorized to transact business in the Commonwealth shall pay the annual registration fee required by law on or before the foreign corporation's annual registration fee due date determined in accordance with subsection A of \xA7 13.1-936.1 of each year.","C. If any foreign corporation whose registered agent has filed with the Commission his statement of resignation pursuant to \xA7 13.1-927 fails to file a statement of change pursuant to \xA7 13.1-926 within 31 days after the date on which the statement of resignation was filed, the Commission shall mail notice to the foreign corporation of impending revocation of its certificate of authority. If the foreign corporation fails to file the statement of change before the last day of the second month immediately following the month in which the impending revocation notice was mailed, the foreign corporation shall automatically cease to be authorized to transact business in the Commonwealth and its certificate of authority shall be automatically revoked as of that day.","D. The automatic revocation of a foreign corporation's certificate of authority pursuant to this section constitutes the appointment of the clerk of the Commission as the foreign corporation's agent for service of process in any proceeding based on a cause of action arising during the time the foreign corporation was authorized to transact business in the Commonwealth. Service of process on the clerk of the Commission under this subsection is service on the foreign corporation and shall be made on the clerk in accordance with \xA7 12.1-19.1.","E. Revocation of a foreign corporation's certificate of authority pursuant to this section does not terminate the authority of the registered agent of the corporation."]},{title:"\xA7 13.1-931. Involuntary revocation of certificate of authority.",text:["A. The certificate of authority to transact business in the Commonwealth of any foreign corporation may be revoked by order of the Commission when it finds that the corporation:","1. Has continued to exceed the authority conferred upon it by law;","2. Has failed to maintain a registered office or a registered agent in the Commonwealth as required by law;","3. Has failed to file any document required by this Act to be filed with the Commission;","4. No longer exists under the laws of the state or country of its incorporation; or","5. Has been convicted for a violation of 8 U.S.C. \xA7 1324a(f), as amended, for actions of its officers and directors constituting a pattern or practice of employing unauthorized aliens in the Commonwealth.","A certificate revoked pursuant to subdivision A 5 shall not be eligible for reinstatement for a period of not less than one year.","B. Any foreign corporation convicted of the offense listed in subdivision A 5 shall immediately report such conviction to the Commission and file with the Commission an authenticated copy of the judgment or record of conviction.","C. Before entering any such order the Commission shall issue a rule against the corporation giving it an opportunity to be heard and show cause why such an order should not be entered. The Commission may issue the rule on its own motion or on motion of the Attorney General.","D. The authority of a foreign corporation to transact business in the Commonwealth ceases on the date shown on the order revoking its certificate of authority.","E. The Commission's revocation of a foreign corporation's certificate of authority appoints the clerk of the Commission the foreign corporation's agent for service of process in any proceeding based on a cause of action arising during the time the foreign corporation was authorized to transact business in the Commonwealth. Service of process on the clerk of the Commission under this subsection is service on the foreign corporation and shall be made on the clerk in accordance with \xA7 12.1-19.1.","F. Revocation of a foreign corporation's certificate of authority does not terminate the authority of the registered agent of the corporation."]},{title:"\xA7 13.1-931.1. Reinstatement of foreign corporation whose certificate of authority has been withdrawn or revoked.",text:["A. A foreign corporation whose certificate of authority to transact business in the Commonwealth has been withdrawn or revoked may be relieved of the withdrawal or revocation and have its certificate of authority reinstated by the Commission within five years after the date of withdrawal or revocation unless the certificate of authority was revoked by order of the Commission pursuant to subdivision A 1 of \xA7 13.1-931.","B. To have its certificate of authority reinstated, a foreign corporation shall provide the Commission with the following:","1. An application for reinstatement, which shall include the identification number issued by the Commission to the corporation, and which may be in the form of a letter signed by an officer or director of the corporation, or which may be by affidavit signed by an agent of any member's interests stating that after diligent search by such agent, no officer or director can be found;","2. A reinstatement fee of $10;","3. All annual registration fees and penalties that were due before the certificate of withdrawal was issued or the certificate of authority was revoked and that would have been assessed or imposed to the date of reinstatement if the corporation had not withdrawn or had its certificate of authority revoked;","4. An annual report for the calendar year that corresponds to the calendar year of the latest annual registration fee that was assessed or that would have been assessed to the date of reinstatement;","5. A duly authenticated copy of any amendments or corrections made to the articles of incorporation or other constituent documents of the foreign corporation and any mergers entered into by the foreign corporation from the date of withdrawal or revocation of its certificate of authority to the date of its application for reinstatement, along with an application for an amended certificate of authority if required as a result of an amendment or a correction, and all fees required by this chapter for the filing of such instruments;","6. If the name of the foreign corporation does not comply with the provisions of \xA7 13.1-924 at the time of reinstatement, an application for an amended certificate of authority to adopt a designated name for use in the Commonwealth that satisfies the requirements of \xA7 13.1-924, with the fee required by this chapter for the filing of an application for an amended certificate of authority; and","7. If the foreign corporation's registered agent has filed a statement of resignation and a new registered agent has not been appointed, a statement of change pursuant to \xA7 13.1-926.","C. If the foreign corporation complies with the provisions of this section, the Commission shall enter an order of reinstatement, reinstating the foreign corporation's certificate of authority to transact business in the Commonwealth."]}];

