var vncaText171=[{title:"\xA7 13.1-944.1. Definitions.",text:["In this article:","\"Articles of organization\" has the same meaning specified in \xA7 13.1-1002.","\"Converting entity\" means the domestic corporation that adopts a plan of entity conversion pursuant to this article.","\"Corporation\" has the same meaning specified in \xA7 13.1-803.","\"Limited liability company\" has the same meaning specified in \xA7 13.1-1002.","\"LLC membership interest\" has the same meaning as membership interest in \xA7 13.1-1002.","\"Member\" when used with respect to a corporation has the meaning as specified in \xA7 13.1-803, and when used with respect to a limited liability company has the same meaning specified in \xA7 13.1-1002.","\"Membership interest\" has the same meaning specified in \xA7 13.1-803.","\"Person\" has the same meaning specified in \xA7 13.1-803.","\"Resulting entity\" means the limited liability company that is in existence immediately after consummation of an entity conversion pursuant to this article."]},{title:"\xA7 13.1-944.2. Entity conversion.",text:["A domestic corporation may become a domestic limited liability company pursuant to a plan of entity conversion that is adopted and approved by the corporation in accordance with the provisions of this article."]},{title:"\xA7 13.1-944.3. Plan of entity conversion.",text:["A. To become a domestic limited liability company, a domestic corporation shall adopt a plan of entity conversion setting forth:","1. A statement of the corporation's intention to convert to a limited liability company;","2. The terms and conditions of the conversion, including the manner and basis of converting the membership interests, if any, of the corporation into LLC membership interests of the resulting entity;","3. If the corporation has no members, the designation of each person who is to become a member of the limited liability company upon conversion, provided that no person shall be designated as a member of the resulting entity without the person's prior consent;","4. As a separate attachment to the plan, the full text of the articles of organization of the resulting entity as they will be in effect upon consummation of the conversion; and","5. Any other provision relating to the conversion that may be desired.","B. The plan of entity conversion may also include a provision that the board of directors may amend the plan before the effective time and date of the certificate of entity conversion. An amendment made after the submission of the plan to the members shall not alter or change any of the terms or conditions of the plan if the change would adversely affect the membership interests of the corporation, unless the amendment has been approved by the members in the manner set forth in \xA7 13.1-944.4."]},{title:"\xA7 13.1-944.4. Action on plan of entity conversion.",text:["A. Where the corporation has no members, or no members having voting rights, the plan shall be adopted upon receiving the vote of at least two-thirds of the directors in office.","B. Where there are members of the corporation having voting rights:","1. The plan of entity conversion shall be adopted by the board of directors;","2. After adopting the plan of entity conversion, the board of directors shall submit the plan to the members for their approval. The board of directors shall also transmit to the members a recommendation that the members approve the plan, unless the board of directors determines that because of conflicts of interest or other special circumstances it should not make such a recommendation, in which case the board of directors shall transmit to the members the basis for that determination; and","3. The voting members shall approve the plan as provided in subdivision C 3.","C. When a plan of entity conversion is to be approved by the members in accordance with subsection B:","1. The board of directors may condition its submission of the plan of entity conversion to the members on any basis;","2. The corporation shall notify each member, whether or not entitled to vote, of the proposed members' meeting in accordance with \xA7 13.1-842 at which the plan of entity conversion is to be submitted for approval. The notice shall also state that the purpose, or one of the purposes, of the meeting is to consider the plan and shall contain or be accompanied by a copy of the plan; and","3. Unless this chapter or the board of directors, acting pursuant to subdivision 1, requires a greater vote, the plan of entity conversion shall be approved by each voting group entitled to vote on the plan by more than two-thirds of all the votes entitled to be cast by that voting group. The articles of incorporation may provide for a greater or lesser vote than that provided for in this subsection or a vote by separate voting groups so long as the vote provided for is not less than a majority of all the votes cast on the plan by each voting group entitled to vote on the plan at a meeting at which a quorum of the voting group exists."]},{title:"\xA7 13.1-944.5. Articles of entity conversion.",text:["A. After the plan of entity conversion of a corporation into a limited liability company has been adopted and approved as required by this article, the converting entity shall deliver to the Commission for filing articles of entity conversion setting forth:","1. The name of the corporation immediately before the filing of the articles of entity conversion and the name to which the name of the converting entity is to be changed, which name shall satisfy the requirements of the laws of the Commonwealth;","2. The date on which the corporation was originally incorporated, organized, or formed; its original name, entity type, and jurisdiction of incorporation, organization, or formation; and, for each subsequent change of entity type or jurisdiction of incorporation, organization, or formation made before the filing of the articles of entity conversion, the effective date of the change and the corporation's name, entity type, and jurisdiction of incorporation, organization, or formation upon consummation of the change;","3. The plan of entity conversion, including the full text of the articles of organization of the resulting entity that comply with the requirements of Chapter 12 (\xA7 13.1-1000 et seq.), as they will be in effect upon consummation of the conversion;","4. The date the plan of entity conversion was approved; and","5. A statement:","a. That the plan was adopted by the vote of at least two-thirds of the directors in office, including the reason member approval was not required;","b. That the plan was adopted by the unanimous consent of the members having voting rights; or","c. That the plan was proposed by the board of directors and submitted to the members in accordance with this chapter, and a statement of:","(1) The existence of a quorum of each voting group entitled to vote separately on the plan; and","(2) Either the total number of votes cast for and against the plan by each voting group entitled to vote separately on the plan or the total number of undisputed votes cast for the plan separately by each voting group and a statement that the number cast for the plan by each voting group was sufficient for approval by that voting group.","B. If the Commission finds that the articles of entity conversion comply with the requirements of law and that all required fees have been paid, it shall issue a certificate of entity conversion."]},{title:"\xA7 13.1-944.6. Effect of entity conversion.",text:["A. When an entity conversion under this article becomes effective, with respect to that entity:","1. The title to all real estate and other property remains in the resulting entity without reversion or impairment;","2. The liabilities remain the liabilities of the resulting entity;","3. A pending proceeding may be continued by or against the resulting entity as if the conversion did not occur;","4. The articles of organization attached to the articles of entity conversion constitute the articles of organization of the resulting entity;","5. The membership interests, if any, of the corporation are reclassified into LLC membership interests in accordance with the plan of entity conversion, and the members of the converting entity are entitled only to the rights provided in the plan of entity conversion;","6. The resulting entity is deemed to:","a. Be a limited liability company for all purposes;","b. Be the same entity without interruption as the converting entity that existed before the conversion; and","c. Have been organized on the date that the converting entity was originally incorporated, organized, or formed; and","7. The corporation shall cease to be a corporation when the certificate of entity conversion becomes effective.","B. Any member of a converting entity who, before the conversion, was liable for the liabilities or obligations of the converting entity is not released from those liabilities or obligations by reason of the conversion."]},{title:"\xA7 13.1-944.7. Abandonment of entity conversion.",text:["A. Unless otherwise provided in a plan of entity conversion of a domestic corporation to become a limited liability company, after the plan has been approved and adopted as required by this article, and at any time before the certificate of entity conversion has become effective, the conversion may be abandoned by the corporation without action by the members in accordance with any procedures set forth in the plan or, if no procedures are set forth in the plan of entity conversion, in the manner determined by the board of directors.","B. If an entity conversion is abandoned under subsection A after articles of entity conversion have been filed with the Commission but before the certificate of entity conversion has become effective, a statement that the entity conversion has been abandoned in accordance with this section shall be delivered to the Commission for filing before the effective time and date of the certificate of entity conversion. Upon filing, the statement shall take effect and the entity conversion shall be deemed abandoned and shall not become effective."]}];

