export const cicbText = [
  {
    title: ` 55-528. Definitions.`,
    text: [
      `As used in this chapter, unless the context requires a different meaning:`,
      `"Association" includes condominium, cooperative, or property owners' associations.`,
      `"Balance of the fund" means cash, securities that are legal investments for fiduciaries under the provisions of subdivisions A 1, 2, and 4 of  2.2-4519, and repurchase agreements secured by obligations of the United States government or any agency thereof, and shall not mean accounts receivable, judgments, notes, accrued interest, or other obligations to the fund.`,
      `"Board" means the Common Interest Community Board.`,
      `"Claimant" means upon proper application to the Director, a receiver for a common interest community manager appointed pursuant to  54.1-2353 in those cases in which there are not sufficient funds to restore all funds that were or ought to have been held in a fiduciary capacity by the subject common interest community manager or to pay an award of reasonable fees, costs, and expenses to the receiver.`,
      `"Common interest community" means real estate located within the Commonwealth subject to a declaration which contains lots, at least some of which are residential or occupied for recreational purposes, and common areas to which a person, by virtue of his ownership of a lot, is a member of an association and is obligated to pay assessments provided for in a declaration.`,
      `"Declaration" means any instrument, however denominated, recorded among the land records of the county or city in which the development or any part thereof is located, that either (i) imposes on the association maintenance or operational responsibilities for the common area as a regular annual assessment or (ii) creates the authority in the association to impose on lots, or on the owners or occupants of such lots, or on any other entity any mandatory payment of money as a regular annual assessment in connection with the provision of maintenance or services or both for the benefit of some or all of the lots, the owners or occupants of the lots, or the common area. "Declaration" includes any amendment or supplement to the instruments described in this definition.`,
      `"Director" means the Director of the Department of Professional and Occupational Regulation.`,
      `"Governing board" means the governing board of an association, including the executive organ of a condominium unit owners' association, the executive board of a cooperative proprietary lessees' association, and the board of directors of a property owners' association.`,
      `"Lot" means (i) any plot or parcel of land designated for separate ownership or occupancy shown on a recorded subdivision plat for a development or the boundaries of which are described in the declaration or in a recorded instrument referred to or expressly contemplated by the declaration, other than a common area, and (ii) a unit in a condominium association or a unit in a real estate cooperative.`,
    ]
  },
  {
    title: ` 55-529. Common Interest Community Management Information Fund.`,
    text: [
      `There is hereby created the Common Interest Community Management Information Fund to be used in the discretion of the Board to promote the improvement and more efficient operation of common interest communities through research and education. The Fund shall consist of money paid into it pursuant to  54.1-2349, 55-79.93:1, 55-504.1, and 55-516.1. The Fund shall be established on the books of the Comptroller, and any funds remaining in such Fund at the end of the biennium shall not revert to the general fund but, at the discretion of the Board, shall remain in the Fund or shall be transferred to the Common Interest Community Management Recovery Fund established pursuant to  55-530.1. Interest earned on the Fund shall be credited to the Fund.`,
    ]
  },
  {
    title: ` 55-530. Powers of the Board; Common interest community ombudsman; final adverse decisions.`,
    text: [
      `A. The Board shall administer the provisions of this chapter pursuant to the powers conferred by  54.1-2349 and this chapter.,`,
      `B. The Director in accordance with  54.1-303 shall appoint a Common Interest Community Ombudsman (the Ombudsman) and shall establish the Office of the Common Interest Community Ombudsman. The Ombudsman shall be a member in good standing in the Virginia State Bar. All state agencies shall assist and cooperate with the Office of the Common Interest Community Ombudsman in the performance of its duties under this chapter. The expenses for the operations of the Office of the Common Interest Community Ombudsman, including the compensation paid to the Ombudsman, shall be paid first from interest earned on deposits constituting the fund and the balance from the moneys collected annually in the fund.`,
      `C. The Office of the Common Interest Community Ombudsman shall:`,
      `1. Assist members in understanding their rights and the processes available to them according to the laws and regulations governing common interest communities;`,
      `2. Answer inquiries from members and other citizens by telephone, mail, electronic mail, and in person;`,
      `3. Provide to members and other citizens information concerning common interest communities upon request;`,
      `4. Make available, either separately or through an existing Internet website utilized by the Director, information as set forth in subdivision 3 and such additional information as may be deemed appropriate;`,
      `5. Receive the notices of final adverse decisions;`,
      `6. In conjunction with complaint and inquiry data maintained by the Director, maintain data on inquiries received, the types of assistance requested, notices of final adverse decisions received, any actions taken, and the disposition of each such matter;`,
      `7. Upon request, assist members in understanding the rights and processes available under the laws and regulations governing common interest communities and provide referrals to public and private agencies offering alternative dispute resolution services, with a goal of reducing and resolving conflicts among associations and their members;`,
      `8. Ensure that members have access to the services provided through the Office of the Common Interest Community Ombudsman and that the members receive timely responses from the representatives of the Office of the Common Interest Community Ombudsman to the inquiries;`,
      `9. Upon request to the Director by (i) any of the standing committees of the General Assembly having jurisdiction over common interest communities or (ii) the Housing Commission, provide to the Director for dissemination to the requesting parties assessments of proposed and existing common interest community laws and other studies of common interest community issues;`,
      `10. Monitor changes in federal and state laws relating to common interest communities;`,
      `11. Provide information to the Director that will permit the Director to report annually on the activities of the Office of the Common Interest Community Ombudsman to the standing committees of the General Assembly having jurisdiction over common interest communities and to the Housing Commission. The Director's report shall be filed by December 1 of each year, and shall include a summary of significant new developments in federal and state laws relating to common interest communities each year; and`,
      `12. Carry out activities as the Board determines to be appropriate.`,
      `D. The Board may use the remainder of the interest earned on the balance of the fund and of the moneys collected annually and deposited in the fund for financing or promoting the following:`,
      `1. Information and research in the field of common interest community management and operation;`,
      `2. Expeditious and inexpensive procedures for resolving complaints about an association from members of the association or other citizens;`,
      `3. Seminars and educational programs designed to address topics of concern to community associations; and`,
      `4. Other programs deemed necessary and proper to accomplish the purpose of this chapter.`,
      `E. The Board shall establish by regulation a requirement that each association shall establish reasonable procedures for the resolution of written complaints from the members of the association and other citizens. Each association shall adhere to the written procedures established pursuant to this subsection when resolving association member and citizen complaints. The procedures shall include but not be limited to the following:`,
      `1. A record of each complaint shall be maintained for no less than one year after the association acts upon the complaint.`,
      `2. Such association shall provide complaint forms or written procedures to be given to persons who wish to register written complaints. The forms or procedures shall include the address and telephone number of the association or its common interest community manager to which complaints shall be directed and the mailing address, telephone number, and electronic mail address of the Office of the Common Interest Community Ombudsman. The forms and written procedures shall include a clear and understandable description of the complainant's right to give notice of adverse decisions pursuant to this section.`,
      `F. A complainant may give notice to the Board of any final adverse decision in accordance with regulations promulgated by the Board. The notice shall be filed within 30 days of the final adverse decision, shall be in writing on forms prescribed by the Board, shall include copies of all records pertinent to the decision, and shall be accompanied by a $25 filing fee. The fee shall be collected by the Director and paid directly into the state treasury and credited to the Common Interest Community Management Information Fund,  55-530.1. The Board may, for good cause shown, waive or refund the filing fee upon a finding that payment of the filing fee will cause undue financial hardship for the member. The Director shall provide a copy of the written notice to the association that made the final adverse decision.`,
      `G. The Director or his designee may request additional information concerning any notice of final adverse decision from the association that made the final adverse decision. The association shall provide such information to the Director within a reasonable time upon request. If the Director upon review determines that the final adverse decision may be in conflict with laws or regulations governing common interest communities or interpretations thereof by the Board, the Director may, in his sole discretion, provide the complainant and the association with information concerning such laws or regulations governing common interest communities or interpretations thereof by the Board. The determination of whether the final adverse decision may be in conflict with laws or regulations governing common interest communities or interpretations thereof by the Board shall be a matter within the sole discretion of the Director, whose decision is final and not subject to further review. The determination of the Director shall not be binding upon the complainant or the association that made the final adverse decision.`,
      `H. The Board shall issue a certificate of filing to each association which has properly filed in accordance with this title. The certificate shall include the date of registration and a unique registration number assigned by the Board.`,
      `I. The Board may prescribe regulations which shall be adopted, amended or repealed in accordance with the Administrative Process Act ( 2.2-4000 et seq.) to accomplish the purpose of this chapter.`,
    ]
  },
  {
    title: ` 55-530.1. Common Interest Community Management Recovery Fund.`,
    text: [
      `A. There is hereby created the Common Interest Community Management Recovery Fund (the Fund) to be used in the discretion of the Board to protect the interests of associations.`,
      `B. Each common interest community manager, at the time of initial application for licensure, and each association filing its first annual report after the effective date shall be assessed $25, which shall be specifically assigned to the Fund. Initial payments may be incorporated in any application fee payment or annual filing fee and transferred to the Fund by the Director within 30 days.`,
      `All assessments, except initial assessments, for the Fund shall be deposited within three business days after their receipt by the Director, in one or more federally insured banks, savings and loan associations, or savings banks located in the Commonwealth. Funds deposited in banks, savings institutions, or savings banks, to the extent in excess of insurance afforded by the Federal Deposit Insurance Corporation or other federal insurance agency, shall be secured under the Security for Public Deposits Act ( 2.2-4400 et seq.). The deposit of these funds in federally insured banks, savings and loan associations, or savings banks located in the Commonwealth shall not be considered investment of such funds for purposes of this section. Funds maintained by the Director may be invested in securities that are legal investments for fiduciaries under the provisions of  64.2-1502.`,
      `Interest earned on the deposits constituting the Fund shall be used for administering the Fund. The remainder of this interest, at the discretion of the Board, may be transferred to the Common Interest Community Management Information Fund or accrue to the Fund.`,
      `C. On and after July 1, 2011, the minimum balance of the Fund shall be $150,000. Whenever the Director determines that the principal balance of the Fund is or will be less than such minimum principal balance, the Director shall immediately inform the Board. At the same time, the Director may recommend that the Board transfer a fixed amount from the Common Interest Community Management Information Fund to the Fund to bring the principal balance of the Fund to the amount required by this subsection. Such transfer shall be considered by the Board within 30 days of the notification of the Director.`,
      `D. If any such transfer of funds is insufficient to bring the principal balance of the Fund to the minimum amount required by this section, or if a transfer to the fund has not occurred, the Board shall assess each association and each common interest community manager within 30 days of notification by the Director, a sum sufficient to bring the principal balance of the Fund to the required minimum amount. The amount of such assessment shall be allocated among the associations and common interest community managers in proportion to the each payor's most recently paid annual assessment, or if an association or common interest community manager has not paid an annual assessment previously, in proportion to the average annual assessment most recently paid by associations or common interest community managers respectively. The Board may order an assessment at any time in addition to any required assessment. Assessments made pursuant to this subsection may be issued by the Board (i) after a determination made by it or (ii) at the time of license renewal.`,
      `Notice to common interest community managers and the governing boards of associations of these assessments shall be by first-class mail, and payment of such assessments shall be made by first-class mail addressed to the Director within 45 days after the mailing of such notice.`,
      `E. If any common interest community manager fails to remit the required payment within 45 days of the mailing, the Director shall notify the common interest community manager by first-class mail at the latest address of record filed with the Board. If no payment has been received by the Director within 30 days after mailing the second notice, the license shall be automatically suspended. The license shall be restored only upon the actual receipt by the Director of the delinquent assessment.`,
      `F. If any association fails to remit the required payment within 45 days of the mailing, the Director shall notify the association by first-class mail at the latest address of record filed with the Board. If no payment has been received by the Director within 30 days after mailing the second notice, it shall be deemed a knowing and willful violation of this section by the governing board of the association.`,
      `G. At the close of each fiscal year, whenever the balance of the fund exceeds $5 million, the amount in excess of $5 million shall be transferred to the Virginia Housing Trust Fund established pursuant to Chapter 9 ( 36-141 et seq.) of Title 36. Except for payments of costs as set forth in this chapter and transfers pursuant to this subsection, there shall be no transfers out of the fund, including transfers to the general fund, regardless of the balance of the fund.`,
      `H. A claimant may seek recovery from the fund subject to the following conditions:`,
      `1. A claimant may file a verified claim in writing to the Director for a recovery from the Fund.`,
      `2. Upon proper application to the Director, in those cases in which there are not sufficient funds to pay an award of reasonable fees, costs, and expenses to the receiver or to restore all funds that were or ought to have been held in a fiduciary capacity by the subject common interest community manager, the Director shall report to the Board the amount of any shortfall to the extent that there are not sufficient funds (i) to pay any award of fees, costs, and expenses pursuant to subsection G of  54.1-2353 by the court appointing the receiver; or (ii) to restore all funds that were or ought to have been held in a fiduciary capacity by the subject common interest community manager, as certified by the court appointing the receiver.`,
      `3. If the Board finds there has been compliance with the required conditions, the Board shall issue a directive ordering payment of the amount of such shortfall to the claimant from the fund, provided that in no event shall such payment exceed the balance in the fund. When the fund balance is not sufficient to pay the aggregate amount of such shortfall, the Board shall direct payment shall be applied first in satisfaction of any award of reasonable fees, costs, and expenses to the receiver and second to restore the funds that were or ought to have been held in a fiduciary capacity by the subject common interest community manager. If the Board has reason to believe that there may be additional claims against the fund, the Board may withhold any payment(s) from the fund for a period of not more than one year. After such one-year period, if the aggregate of claims received exceeds the fund balance, the fund balance shall be prorated by the Board among the claimants and paid in the above payment order from the fund in proportion to the amounts of claims remaining unpaid.`,
      `4. The Director shall, subject to the limitations set forth in this subsection, pay to the claimant from the fund such amount as shall be directed by the Board upon the execution and delivery to the Director by such claimant of an assignment to the Board of the claimant's rights on its behalf and on behalf of the associations receiving distributions from the fund against the common interest community manager to the extent that such rights were satisfied from the fund.`,
      `5. The claimant shall be notified in writing of the findings of the Board. The Board's findings shall be considered a case decision as defined in  2.2-4001, and judicial review of these findings shall be in accordance with  2.2-4025 of the Administrative Process Act ( 2.2-4000 et seq.).`,
      `6. Notwithstanding any other provision of law, the Board shall have the right to appeal a decision of any court that is contrary to any distribution recommended or authorized by it.`,
      `7. Upon payment by the Director to a claimant from the fund as provided in this subsection, the Board shall immediately revoke the license of the common interest community manager whose actions resulted in payment from the fund. The common interest community manager whose license was so revoked shall not be eligible to apply for a license as a common interest community manager until he has repaid in full the amount paid from the fund on his account, plus interest at the judgment rate of interest from the date of payment from the fund.`,
      `8. Nothing contained in this subsection shall limit the authority of the Board to take disciplinary action against any common interest community manager for any violation of statute or regulation, nor shall the repayment in full by a common interest community manager of the amount paid from the fund on such common interest community manager's account nullify or modify the effect of any disciplinary proceeding against such common interest community manager for any such violation.`,
    ]
  }
];



